@import "./../../../styles/commonStyle/commonStyle.scss";
div.mini-cart-container {
  max-width: 1300px !important;
  position: relative;
  margin-bottom: 0 !important;
}
.mini-cart {
  position: fixed;
  top: 72px;
  left: calc((100vw) - (50vw - 395px));
  width: 320px;
  border: 1px solid #ccc;
  z-index: 5;
  border-radius: 0 0 5px 5px;
  box-shadow: 0px 3px 6px #00000029 !important;
  @media (max-width: 1440px) {
    left: auto;
    right: 0;
  }
  @media (max-width: 767px) {
    position: fixed;
    top: auto;
    bottom: 0;
    left: 0;
    width: 100%;
    box-shadow: none !important;
    border: none;
  }
  &.open {
    @media (max-width: 767px) {
      background-color: rgba(0, 0, 0, 0.5);
      height: calc(100vh - 50px);
      display: flex;
      flex-flow: column;
      justify-content: flex-end;
    }
  }
  &-info {
    flex-grow: 0;
    flex: 1;
    padding-right: 5px;
  }
  &-qty {
    max-width: 100px;
    flex-basis: 100px;
    .quantity-count-contain {
      margin-top: 0;
    }
  }
  &-body {
    background-color: $white;
    @include scrollbars(0.5em, $scroll-bar);
    height: 100%;
    max-height: 200px;
    overflow-y: auto;
    @media (max-width: 767px) {
      max-height: calc(100vh - 250px);
      overflow: auto;
      width: calc(100% - 24px);
      margin: 0 auto;
      box-shadow: 0px 0px 6px #00000029;
      border-radius: 6px 6px 0px 0px;
    }
    .no-items {
      padding-top: 10px;
      padding-bottom: 10px;
    }
  }
  &-single {
    display: flex;
    background-color: $white;
    padding: 5px 20px;
    border-bottom: 1px solid #f0f0f0;
    align-items: center;
    &:first-child {
      padding-top: 15px;
      @media (max-width: 767px) {
        padding-top: 15px;
      }
    }
    &:last-child {
      @media (max-width: 767px) {
        padding-bottom: 15px;
      }
    }
    .product-brand {
      font-size: $font12;
      color: $color-grey-dark;
      @media (max-width: 767px) {
        font-size: $font14;
      }
    }
    .product-heading {
      display: block;
      width: 100%;
      position: relative;
      min-height: 40px;
      font-size: $font14;
      line-height: $font20;
      color: $color-grey-dark;
      @media (max-width: 767px) {
        font-size: $font16;
        min-height: auto;
      }
    }
    .quantity-count-wrapper {
      button {
        color: $black-light;
        border: 1px solid $black-light;
        height: 24px;
        width: 24px;
        min-width: auto;
      }
      input {
        color: $blue;
        width: 44px;
      }
    }
  }
  &-footer {
    background-color: #fff;
    padding: 2px 20px;
    background-image: linear-gradient(to right, #f7d43e, #f3a820);
    @media (max-width: 767px) {
      padding: 10px 20px;
    }
  }
  &-checkout {
    display: block;
    border: 1px solid $black-light;
    border-radius: 5px;
    text-align: center;
    text-decoration: none;
    text-transform: capitalize;
    @include standardButton($black, $white, $white, $black);
    padding: 5px;
    font-weight: 600;
    font-size: $font16;
    margin-top: 5px;
    @media (max-width: 767px) {
      padding: 8px 5px;
    }
  }
  &-action {
    display: flex;
    margin-top: 5px;
    margin-bottom: 5px;
    align-items: center;
    width: 300px;
    @media (max-width: 767px) {
      margin-bottom: 0;
    }
    button {
      color: $black;
      padding: 0;
      svg {
        font-size: $font24;
      }
    }
  }
  &-text {
    flex: 1;
  }
  &-item {
    font-size: $font16;
    font-weight: 600;
    position: relative;
    &:after {
      content: "|";
      padding-left: 15px;
      padding-right: 15px;
      font-weight: 400;
    }
  }
  &-price {
    font-size: $font16;
    font-weight: 600;
  }
  &-note {
    font-size: 12px;
    padding-right: 25px;
  }
}
.cart-summary-container {
  .quantity-count-wrapper {
    button {
      color: $blue-dark;
    }
    input {
      color: $black-light;
      pointer-events: none;
    }
  }
}
.has-toast {
  .mini-cart {
    top: 119px;
    @media (max-width: 767px) {
      top: auto;
    }
  }
}
