.MuiTooltip-tooltip {
  background-color: rgba(35, 31, 32, 0.8) !important;
  max-width: 230px !important;
  text-align: center;
}

// stepper css
.wizard {
  &-step {
    @include maxContainer;
    min-height:0;
    display: block;
    .step-header {
      border-radius: 6px;
      background-color: #fff;
      padding: 54px 0 50px;
      @media (max-width: 991px) {
        padding: 34px 0 30px;
      }
      .step-header-type {
        background: url(https://share.snacks.com/images/product-page-hero-type.png)
          no-repeat;
        background-position: 50% 50%;
        height: 200px;
      }
      &.shipping-details-old {
        background: url(https://share.snacks.com/images/checkout-hero-background.png) no-repeat;
        margin: 0 0 50px 0;
        .step-header-type {
            background: url(https://share.snacks.com/images/order-details-hero-type.png) no-repeat;
            background-position: 50% 50%;
        }
      }
      &.shipping-details {
        background: white//url(https://share.snacks.com/images/checkout-hero-background.png)
          no-repeat;
        display: block;
        width: 100%;
        .step-header-type {
          background: white;
          display: flex;
          width: 100%;
          align-content: space-around;
          @media (max-width: 767px) {
            display: block;
          }
          &-inner {
            height: 100%;
            justify-content: space-between;
            align-content: flex-end;
            max-width: $mobile-max-width;
            flex-wrap: wrap;
            margin: 0 auto;
            display: flex;
            padding: 15px 0;
            &-title {
              font-size: 2.15em;
              color: $color-blue-header;
              align-self: flex-end;
              font-family: railroad-gothic-atf, sans-serif;
              font-style: normal;
              font-weight: 700;
            }
            &-btn-container {
              align-self: flex-end;
            }
          }
        }
      }
      &.thank-you {
        background: url(https://share.snacks.com/images/confirmation-hero-background.png)
          no-repeat;
        margin: 0;
        @media (max-width: $mobile-max-width) {
          margin-bottom: 0 !important;
        }
        .step-header-type {
          background: url(https://share.snacks.com/images/confirmation-hero-type.png)
            no-repeat;
          background-position: 50% 50%;
          @media (max-width: $mobile-max-width) {
            background-size: 440px !important;
          }
          @media (max-width: 480px) {
            background-size: 320px !important;
          }
        }
      }
      &-profile {
        .step-header-type {
          top: 97px;
          left: 0px;
          width: 1520px;
          height: 227px;
          background: #FFFFFF 0% 0% no-repeat padding-box;
          box-shadow: 0px 2px 3px #00000033;
        }
        & + div:not(.account-info) {
          margin-bottom: 0px !important;
        }
      }
    }
    &-list {
      min-height: 800px;
      @media (max-width: $mobile-max-width) {
        min-height: 500px;
      }
    }
  }
  &-buttons {
    display: flex;
    justify-content: flex-end;
    padding-top: 46px;
    padding-bottom: 120px;
    width: 984px;
    margin: 0 auto;
    @media (max-width: $mobile-max-width) {
      padding: 12px 16px;
      width: 100%;
      display: flex;
    }
    &:empty {
      display: none;
    }
    .btn-mui {
      font-size: 1rem;
      font-weight: 500;
      text-align: center;
      color: #231f20;
      text-transform: none;
      @media (max-width: $mobile-max-width) {
        font-size: 0.875rem;
      }
      @media (max-width: 767px) {
        padding: 6px 5px;
      }
      &:not(:last-child) {
        margin-right: 20px;
        @media (max-width: $mobile-max-width) {
          margin-right: 10px;
        }
      }
    }
  }
}

// checkout accordion
.checkout {
  &-view {
    .checkout-content {
      &-body {
        margin: 15px 0 25px 0;
        &-inner {
          border-radius: 18px;
          padding: 22px;
          box-shadow: 0px 1px 3px #00000033;
          @media (max-width: 767px) {
            padding: 15px 20px 15px 20px;
          }
          &.full-view {
            padding: 10px;
            .form-card {
              padding-bottom: 20px;
            }
            .button-group {
              margin-top: 5px;
              display: block;
              @media (max-width: $mobile-max-width) {
                padding-top: 10px;
              }
              .btn-mui {
                @media (max-width: 420px) {
                  min-width: 100px;
                }
              }
            }
          }
        }
        .form-card-container {
          width: calc(100% + 16px) !important;
          align-self: center;
          &.has-billing {
            .radio-group-shipping {
              margin-top: 0;
            }
          }
          & > div {
            padding: 8px !important;
            &.view-only {
              padding-bottom: 0 !important;
            }
          }
        }
        .btn-link {
          @media (max-width: 991px) {
            font-size: 1rem;
          }
        }
      }
    }
    h2 {
      color: #4b4b4b;
      font-size: 1.5em;
      font-weight: 600;
      margin-bottom: 15px;
    }
    h3 {
      margin: 0;
      font-weight: 600;
      color: #4b4b4b;
      font-size: 1.25em;
      @media (max-width: 991px) {
        font-size: 1.2rem;
      }
    }
    .button-group {
      padding-left: 0 !important;
      padding-right: 0 !important;
      display: flex;
      @media (max-width: 767px) {
        justify-content: flex-end;
      }
      .btn-mui {
        margin: 0 9px;
        flex: 1;
        @media (max-width: 991px) {
          font-size: 1rem;
        }
        @media (max-width: 767px) {
          font-size: 14px;
          flex: none;
        }
        @media (max-width: 374px) {
          margin: 0 5px;
          padding-left: 5px;
          padding-right: 5px;
        }
        &:first-child {
          margin-left: 0;
          @media (max-width: 420px) {
            margin-right: 0 !important;
          }
        }
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
  &-flexContainer {
    justify-content: space-between;
    button {
      color: #4b4b4b;
      text-transform: capitalize;
      font-size: 18px;
      opacity: 1;
      padding-top: 7px;
      padding-bottom: 7px;
      min-height: 42px;
      min-width: 130px;
      font-weight: 600;
      border-radius: 0;
      @media (max-width: $mobile-max-width) {
        min-width: 25%;
        padding-left: 8px;
        padding-right: 8px;
        font-size: 16px;
      }
      @media (max-width: 767px) {
        padding-left: 12px;
        padding-right: 12px;
      }
      @media (max-width: 413px) {
        font-size: 14px;
      }
      @media (max-width: 359px) {
        min-width: 70px;
        padding-left: 5px;
        padding-right: 5px;
      }
      &:first-child {
        border-radius: 8px 0 0 8px;
      }
      &:last-child {
        border-radius: 0 8px 8px 0;
      }
      &.Mui-selected {
        background-color: $color-yellow;
      }
    }
  }
  &-indicator {
    background-color: #6f6f6f !important;
    height: 3px !important;
    border-radius: 0;
  }
  &-root {
    margin-bottom: 15px;
    background-color: #fff;
    border-radius: 8px;
    border: 1px solid #f0f0f0;
  }
  &-container {
    padding-bottom: 120px !important;
    position: relative;
    & > div {
      margin: 0;
    }
    @media (max-width: 767px) {
      .summary-col {
        order: 1;
        max-width: 100%;
        flex-basis: 100%;
        padding: 0 0 0 0 !important;
        margin-bottom: 15px;
        
      }
      .registration-col {
        order: 2;
        max-width: 100%;
        flex-basis: 100%;
      }
    }
    @media (max-width: $mobile-max-width) {
      margin-bottom: 0 !important;
      padding-bottom: 0 !important;
    }
  }
  &-content {
    &.info-box {
      box-shadow: 0px 1px 3px #00000033;
      border-radius: 16px;
      .checkout-content-head {
        box-shadow: none;
        border-radius: 16px 16px 0 0;
      }
      .checkout-content-body-inner {
        box-shadow: none;
        border-radius: 0 0 16px 16px;
      }
      .button-group {
        padding-left: 0;
        padding-right: 0;
      }
    }
    .form-card-container {
      width: calc(100% + 24px) !important;
      margin: -12px !important;
      & > div {
        padding: 12px !important;
      }
    }
    .form-card .form-card-heading {
      font-size: 1em;
    }
    &-wrapper {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
      &.collapsed {
        .checkout-content-head {
          border-radius: 18px;
          &:after {
            display: none;
          }
        }
      }
    }
    &-head {
      display: flex;
      align-items: center;
      background-color: transparent;
      padding: 15px 20px;
      // border-radius: 28px 28px 0 0 ;
      box-shadow: none;
      // border-radius: 28px 28px 0 0 ;
      // box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.24);
