      @media (max-width: 767px) {
        padding: 20px 18px 15px 18px;
      }
      &:focus {
        outline: none;
        .check-icn {
          outline: -webkit-focus-ring-color auto 1px;
        }
      }
      &:after {
        content: "";
        height: 5px;
        width: 100%;
        position: absolute;
        bottom: -5px;
        left: 0;
        background: #fff;
      }
      h2 {
        color: $color-primary;
        font-size: 21px;
        margin: 0 0 0 5px;
        font-weight: 600;
        @media (max-width: 767px) {
          font-size: 18px;
          margin: 0;
        }
      }
      .check-icn {
        height: 28px;
        width: 29px;
        border: 2px solid #707070;
        border-radius: 50%;
        position: relative;
        @media (max-width: 767px) {
          height: 20px;
          width: 20px;
        }
        &.checked {
          &:before {
            content: "";
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            height: 16px;
            width: 16px;
            background-color: $color-primary;
            border-radius: 50%;
            @media (max-width: 767px) {
              height: 10px;
              width: 10px;
            }
          }
        }
      }
    }
    &-body {
      .has-guest {
        display: none;
      }
      .login-form {
        &.custom-form {
          .info0 {
            display: none;
          }
          .form-label {
            font-size: 18px !important;
            transform: translate(20px, 16px) scale(1);
            color: $black-medium;
            font-weight: 500 !important;
            &[class*="-shrink"] {
              transform: translate(20px, -8px) scale(0.85);
              font-weight: 400 !important;
            }
          }
          input {
            height: 20px;
          }
          .form-card-container {
            width: calc(100% + 28px) !important;
            margin: -14px !important;
            & > div {
              padding: 14px !important;
            }
          }
          .signin-text {
            margin-top: 25px;
            font-size: 18px;
            @media (max-width: 767px) {
              font-size: 16px;
            }
            button,
            a {
              font-size: 18px;
              @media (max-width: 767px) {
                margin-left: 10px;
                font-size: 16px;
              }
            }
          }
        }
        .form-card {
          border-bottom: none;
          padding-bottom: 10px;
        }
        .button-group {
          margin-top: 20px;
          text-align: left;
          color: $color-primary;
          font-size: 16px;
          font-weight: 600;
          padding: 0;
          text-transform: capitalize;
          a {
            &:hover,
            &:focus {
              color: #af411d;
            }
          }
        }
        .btn-mui {
          background-color: $color-yellow;
          border-color: $color-yellow;
          color: #4b4b4b !important;
          font-size: 18px;
          &:hover,
          &:focus {
            background-color: $color-yellow-hover;
            border-color: $color-yellow-hover;
          }
          @media (max-width: 767px) {
            padding-top: 5px;
            padding-bottom: 5px;
            font-size: 16px;
          }
        }
      }

      .form-group {
        input,
        select {
          font-size: 1em;
          padding: 16.5px 18px;
          font-weight: 500;
        }
        .form-label {
          font-size: 1em !important;
          transform: translate(12px, 18px) scale(1);
          &[class*="-shrink"] {
            transform: translate(12px, -4px) scale(0.75);
            font-weight: 500 !important;
          }
        }
      }
      .react-select-big {
        div[class*="-singleValue"] {
          font-size: 1em;
          font-weight: 500;
        }
        div[class*="-control"] > div:first-child {
          padding-left: 15px;
        }
      }
      &-inner {
        background-color: transparent;
        box-shadow: none;
        @media all and (-ms-high-contrast: none) {
          box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.24);
        }
        @media (max-width: 767px) {
          padding: 0 0px 16px 0px;
        }
        > .form-card {
          @include boxShadowContainer;
          padding: 30px;
          margin-bottom: 20px;
        }
        &.inner-view{
          .form-card{
            border:none;
            box-shadow: 0 0 0;
            background: transparent;
            border-radius: 0;
            padding:0;
          }
        }
