.stepper {
  &-head {
    width: calc(960px - 200px);
    margin: 0 auto;
    justify-content: space-between;
    overflow: hidden;
    padding: 0px !important;
    @media (max-width: $mobile-max-width) {
      width: 100%;
    }
    div[class^="MuiStepConnector"] {
      top: auto;
      bottom: 6px;
      left: calc(-100% - 70px);
      width: calc(100% + 100px);
      @media (max-width: $mobile-max-width) {
        left: calc(-100% - 15px);
        width: calc(100% + 40px);
      }
      @media (max-width: 800px) {
        left: calc(-100% + 30px);
        width: calc(100% - 5px);
      }
      & > span {
        border-top: none;
        background-image: linear-gradient(
          to right,
          #707070 33%,
          rgba(255, 255, 255, 0) 0%
        );
        background-position: bottom;
        background-size: 8px 1px;
        background-repeat: repeat-x;
        height: 1px;
        width: 100%;
        @media (max-width: $mobile-max-width) {
          display: none;
        }
      }
    }
    & > div {
      padding: 0px;
      flex: none;
      &:last-child {
        display: none;
      }
    }
  }
  &-col {
    flex: 1;
    position: relative;
    @media (max-width: $mobile-max-width) {
      max-width: 100%;
      flex-basis: 100%;
    }
    &-header {
      background-color: #fafdff;
      border-bottom: 1px solid $color-border;
      padding: 10px 0 0 0;
      position: relative;
      display: none;
      &.hide {
        display: none;
      }
      @media (max-width: $mobile-max-width) {
        padding: 0 0 15px 0;
      }
      &:after {
        @media (max-width: $mobile-max-width) {
          content: "";
          position: absolute;
          bottom: 23px;
          left: 48px;
          width: calc(100% - 100px);
          height: 1px;
          background-image: linear-gradient(
            to right,
            #707070 33%,
            rgba(255, 255, 255, 0) 0%
          );
          background-position: bottom;
          background-size: 8px 1px;
          background-repeat: repeat-x;
        }
        @media (max-width: 767px) {
          left: 45px;
          width: calc(100% - 80px);
        }
      }
    }
  }
}
.step {
