      &.btn-mui {
        padding-top: 5px;
        padding-bottom: 5px;
        margin-left: 10px;
      }
    }
  }
  &-wrapper {
    @supports (-ms-ime-align: auto) {
      width: 110px;
    }
    @media all and (-ms-high-contrast: none) {
      width: 110px;
      margin: 0 auto;
    }
    &.qty {
      position: relative;
      top: -1px;
    }
    button {
      padding: 0;
      @media (max-width: 767px) {
        height: 24px;
        width: 24px;
      }
