      }
    }
    &.active {
      .mix-count {
        &-title {
          background-color: $color-yellow;
        }
        &-content {
          .mix-count-total-price {
            color: $color-primary;
          }
        }
      }
    }
    &-title {
      background-color: $color-disable;
      padding: 9px 10px;
      -webkit-box-flex: 0;
      -ms-flex: 0 0 100%;
