      max-width: 100%;
      @media (max-width: $mobile-max-width) {
        display: flex;
        align-items: center;
        justify-content: center;
        -ms-flex: 0 0 50%;
        flex: 0 0 50%;
        max-width: 50%;
        padding: 17px 10px;
        border-radius: 0;
      }
      h3 {
        font-size: 1.625rem;
        font-weight: 600;
        color: #231f20;
