    &-total {
      flex: 0 0 100%;
      max-width: 100%;
      @media (max-width: $mobile-max-width) {
        flex: 0 0 50%;
        max-width: 50%;
        order: 2;
      }
      &-text {
        font-size: 1.125rem !important;
        color: #231f20 !important;
        font-weight: 600;
        @media (max-width: $mobile-max-width) {
          display: none;
        }
      }
    }
    &-content {
      display: flex;
      flex-wrap: wrap;
      padding: 0px !important;
      background: #fff;
      border-radius: 28px;
      border: 1px solid $color-disable;
      box-shadow: 0px 3px 6px $color-box-shadow;
      overflow: hidden;
      @media (max-width: $mobile-max-width) {
        border-radius: 0;
      }
      &-inner {
        padding: 0 12px;
        margin: 10px 0;
        min-height: 50px;
        max-height: calc(100vh - 500px);
        overflow: auto;
        -webkit-box-flex: 0;
        -ms-flex: 0 0 100%;
        flex: 0 0 100%;
        max-width: 100%;
        @media (min-height: 750px) {
          max-height: 230px;
        }
        @media (max-height: 600px) {
          max-height: calc(100vh - 700px);
        }
        @media (max-width: $mobile-max-width) {
          max-height: 25vh;
          display: none;
          order: 4;
          &.open {
            display: block;
          }
        }
        &:empty {
          min-height: 50px;
          @media (max-width: $mobile-max-width) {
            display: none;
          }
        }
        & > div {
          margin: 0px;
          width: 100%;
          @media (max-width: $mobile-max-width) {
            width: 100%;
          }
          div {
            &:first-child {
              @media (max-width: $mobile-max-width) {
                max-width: calc(100% - 50px);
              }
            }
            &:last-child {
              width: 40px;
              text-align: right;
            }
          }
        }
      }
      .mix-count-total-price {
        font-size: 40px;
        line-height: 40px;
        font-weight: 700;
        padding-bottom: 8px;
        -webkit-box-flex: 0;
        -ms-flex: 0 0 100%;
        flex: 0 0 100%;
        max-width: 100%;
        display: flex;
        align-items: flex-start;
        justify-content: center;
        padding-top: 5px;
        @media (max-width: $mobile-max-width) {
          font-size: 2.2em;
          line-height: 50px;
          padding-bottom: 0;
          padding-top: 10px;
        }
        .currency {
          font-size: 20px;
          line-height: 26px;
          vertical-align: super;
          font-style: normal;
          font-weight: 700;
        }
      }
      .mix-count-product-text {
        font-weight: 400;
        line-height: 1.235;
        letter-spacing: 0.00735em;
        font-size: 0.75em;
        color: #231f20;

        &.quantity {
          font-weight: 600;
        }
      }
      .mix-count-total-text {
        font-size: 0.75em;
        color: #231f20;

        &.quantity {
          font-weight: 600;
        }
      }
    }
    &-action {
      display: flex;
      flex-direction: column;
      margin-top: 10px;
      padding: 10px 0 0 0;
      .btn-mui {
        height: 100%;
        margin: 0 0 18px 0;
        width: 219px;
        .total-inline {
          display: none;
          font-style: normal;
          @media (max-width: 959px) {
            display: block;
          }
        }
      }
    }
    .btn-mui {
      color: $color-black;
      @media (max-width: $mobile-max-width) {
        border-radius: 0;
      }
      &[disabled] {
        color: $color-grey-dark;
      }
      &.btn-red {
        &[disabled] {
          background-color: $color-disable;
        }
      }
    }
  }

  // modal blur
  &[class^="MuiBackdrop"] {
    -webkit-backdrop-filter: blur(2px);
    backdrop-filter: blur(2px);
  }

  /* product filter */
  &.product-filter {
    @media (max-width: $mobile-max-width) {
      margin-bottom: 0;
      .filter-btn-close {
        display: none;
      }
      .product-filter-buttons {
        width: 100%;
        button {
          width: 100%;
          &:focus,
          &:hover {
            background-color: #fff;
            border-color: #c4c4c4;
          }
        }
      }
    }
    &-buttons {
      display: flex;
      justify-content: space-between;
      @media (max-width: 959px) {
        justify-content: flex-start;
      }
      button {
        min-width: calc(50% - 6px) !important;
        padding-top: 5px;
        padding-bottom: 5px;
        font-size: 1em;
        &:not(:last-child) {
          @media (max-width: $mobile-max-width) {
            margin-right: 10px;
          }
        }
        @media (max-width: 959px) {
          min-width: 100px !important;
          font-size: 14px;
        }
      }
    }
  }

  // phone input and select
  &.phone {
    &-select {
      position: relative;
      &:after {
        content: "\f107";
        font-weight: 700;
        position: absolute;
        display: inline-block;
        top: 50%;
        transform: translateY(-50%);
        right: 10px;
        font-family: "Font Awesome\ 5 Free", sans-serif;
        font-size: 1.8em;
        -webkit-text-stroke: 1.8px #fff;
        color: $color-grey-dark;
        pointer-events: none;
      }
      fieldset {
        border-radius: 8px 0px 0px 8px;
        border-right: none;
      }
      svg {
        opacity: 0;
      }
      select {
        padding-right: 0px !important;
        width: 60px;
      }
      &-full {
        select {
          width: 100%;
          padding-top: 14.5px;
          height: 20px;
        }
        fieldset {
          border-radius: 8px;
          border-right: 1px solid #707070;
        }
      }
    }
    &-input {
      fieldset {
        border-radius: 0px 8px 8px 0px;
      }
    }
  }
}

