    color: #002855;
    font-weight: 500;
    font-family: "Poppins", sans-serif !important;
    @media (min-width: 991px) {
      padding-top: 4px;
      padding-bottom: 4px;
    }
    div[class*="-icon"] {
        svg{
          display: none;
        }
        &:after{
          content: "\f06a";
          font-weight: 700;
          font-family: "Font Awesome\ 5 Free", sans-serif;
          color: $black;
          font-size: 20px;
          @media (max-width: 991px) {
            font-size: 16px;
          }
        }
    }
  }
  &-full {
    width: 100%;
    top: 0;
    left: 0;
    transform: none;
    & > div {
      width: 100%;
      border-radius: 0;
      justify-content: center;
      align-items: center;
      @media (max-width: 767px) {
        font-size: 12px;
        padding: 2px 5px 2px 10px;
      }
      @media (max-width: 420px) {
        padding-left: 5px;
      }
      div[class*="-icon"] {
        @media (max-width: 767px) {
          margin-right: 5px;
        }
      }
      div[class*="-message"] {
        @media (max-width: 767px) {
          flex: 1;
          padding-right: 30px;
          padding-top: 5px;
          padding-bottom: 5px;
        }
      }
      div[class*="-action"] {
        position: absolute;
        right: 16px;
        @media all and (-ms-high-contrast: none) {
          top: 10px;
        }
      }
    }
  }
  &-inline {
    &.error {
      background-color: $orange-light;
      color: #002855;
      font-weight: 500;
      font-family: "Poppins", sans-serif !important;
      div[class*="-icon"] {
        svg{
          fill: #002855;
        }
      }
    }
    &.success {
      background-color: #4caf50;
      color: #fff;
    }
    font-weight: 500;
    svg {
      fill: #fff;
    }
  }
}

// common card
