        padding: 20px 30px 15px 30px;
        color: #3c3c3c;
        font-size: 15px;
        min-height: 150px;
        @media (max-width: $mobile-max-width) {
          padding: 20px;
          font-size: 14px;
        }
        strong {
          font-weight: 600;
          color: #3c3c3c;
        }
        ul {
          margin-left: 0;
          padding-left: 20px;
        }
        p {
          margin-top: 0;
        }
        u {
          text-decoration: none;
        }
        table {
          margin-bottom: 15px;
          border-color: $color-grey-dark;
          border-collapse: collapse;
          tr {
            &:nth-child(odd) {
              background-color: #f5f5f5;
            }
          }
          td,
          th {
            padding: 5px 10px;
            & > p:last-child {
              margin-bottom: 0;
            }
          }
        }
      }
      &.multi-listed {
        ul {
          ul {
            margin-bottom: 15px;
            margin-top: 5px;
            li {
              margin-bottom: 5px;
            }
          }
        }
      }
      &.faq-box {
        table {
          border-collapse: collapse;
          caption {
            display: none;
          }
          tr {
            &:nth-child(odd) {
              background-color: #fff;
            }
            &.title-row {
              background: #d7e2f2;
              font-weight: 500;
            }
            ol {
              margin-top: 0;
              padding-left: 25px;
              @media (max-width: 767px) {
                padding-left: 15px;
              }
            }
            th,
            td {
              font-size: 0.875em;
              border: 1px solid #4a4a4a;
              text-align: left;
              padding: 5px 10px;
              @media (max-width: 767px) {
                padding: 5px;
              }
              &:empty {
                height: 30px;
              }
            }
            th {
              background: #d9d9d9;
              font-weight: 500;
            }
            td {
              vertical-align: top;
              &:nth-child(1) {
                width: 27%;
              }
              &:nth-child(2) {
                width: 56%;
              }
              &:nth-child(3) {
                width: 17%;
              }
            }
          }
          strong {
            display: block;
            padding: 3px 0;
            color: #333;
          }
        }
      }
    }
    &-contact{
      margin-top: 0;
      @media (max-width: 767px) {
        padding-top: 30px;
      }
      .common-box-body{
        @media (max-width: 767px) {
          margin-bottom: 20px;
        }
      }
    }
  }
}

