            @media (max-width: 767px){
              min-width: auto !important;
              width: 100% !important;
              max-width: none;
            }
          }
          &.btn-sm{
            font-size: $font14;
            min-width: 120px;
            flex: none;
          }
        }
      }
      .page-heading {
        @include pageHeading;
        font-size: $font42;
      }
      .separator {
        @include verticalSeparator($orange);
      }
    }
  }
  
  &-content {
    align-self: center;
    max-width: 1224px;
    @media (max-width: 1200px) {
      width: calc(100% - 20px) !important;
      margin: -20px !important;
    }
    .popular-products{
      @media (max-width: 1025px){
        padding-left: 0;
        padding-right: 0;
      }
    }
    &-container {
      margin-top: 50px;
      @include boxShadowContainer;
      .thankyou-gray {
        background-color: $grey;
      }
      .order-bottom-container {
        padding: 0 50px 30px 50px !important;
        @media (max-width: 768px){
          padding: 0 15px 30px 15px !important;
        }
        @media (max-width: 767px){
          padding: 0 15px 0 15px !important;
        }
        .summary-list-grid{
          &>div{
            &>div{
              @media (max-width: 991px) {
                max-width: 50%;
                flex-basis: 50%;
              }
              @media (max-width: 767px) {
                max-width: 100%;
                flex-basis: 100%;
              }
            }
          }
        }
      }
      .top-container {
        padding: 40px 20px 40px 40px !important;
        @media (max-width: 768px){
          padding: 30px 15px 30px 15px !important;
        }
        @media (max-width: 767px){
          padding: 30px 20px 30px 20px !important;
        }
        .thankyou-content-inside {
          padding-left: 15px;
        }
      }
      .thankyou-content {
        align-content: center;
        padding: 15px 10px 0 20px;
      }
    }
    .summary-img-order{
      max-width: 68px;
      flex-basis: 68px;
      &+div{
        flex: 1;
        max-width: 100%;
        padding-left: 15px;
      }
    }
    .item-desc{
      font-size: $font16;
      color: $black-medium;
      font-weight: 600;
      min-height: 75px;
      @media (max-width: 767px) {
        min-height: auto;
      }
    }
    .item-qty{
      font-size: $font14;
      color: $black-medium;
      min-height: 24px;
      &>span{
        &:after{
          content: '|';
          margin: 0 5px 0 10px;
        }
      }
      .multipack{
        &-view-detail{
          padding: 0 5px;
          text-transform: capitalize;
          min-width: auto;
          color: $blue;
          font-family: "Poppins", sans-serif !important;
          font-weight: 500;
          position: relative;
          top: -1px;
        }
      }
    }
    .popular-products-heading{
      margin-bottom:20px ;
       h3{
        font-size: $font26;
        margin-top: 0;
        @media (max-width: 767px) {
          font-size: $font20;
        }
      }
      .show-all{
        display: none;
      }   
    }
    .summary-list{
      &>div{
        &>div{
          @media (max-width: 768px) {
            max-width: 50%;
            flex-basis: 50%;
          }
          @media (max-width: 767px) {
            max-width: 100%;
            flex-basis: 100%;
          }
          &:last-child{
            padding-bottom: 20px;
            .divider{
              @media (max-width: 767px) {
                display: none;
              }   
            }
          }
        }
      }
    }
    .multipack{
      &-cart{
        &-detail{
          padding: 20px;
          margin-bottom: 15px;
          @media (max-width: 767px) {
            margin-left: 0 !important;
            margin-right: 0 !important;
            max-width: 100% !important;
            flex-basis: 100% !important;
          }
          .title-product{
            font-size: $font14;
            font-weight: 500;
          }
          .count{
            font-size: $font14;
          }
          &-inner{
            &>div{
              &:not(:last-child){
                margin-bottom: 15px;
              }
            }
          }
          &-img{
            width: 70px;
            padding-right: 15px;
          }
          &-close{
            top: 3px;
            right: 3px;
            height: 24px;
            width: 24px;
            i{
              font-size: 18px;
            }
          }
        }
      }
    }
  }
  
  &-image {
    background: url(https://share.snacks.com/images/thank_you_box.png) no-repeat;
    max-width: 100%;
    height: 300px;
    object-fit: cover;
    background-position: center;
    @media (max-width: $mobile-max-width) {
      object-fit: cover;
      max-width: 100%;
      height: 300px;
    }
  }
  &-header {
    height: auto;
    h2 {
      font-size: $font30;
      color: $black-medium;
      margin-bottom: 30px;
    }
  }
  &-line-brown {
    font-size: $font18;
    font-weight: 600;
    color: $color-brown;
    margin-bottom: 26px;
    margin-top: 13px;
    text-align: center;
    span {
      display: inline-block;
    }
  }
  &-line1 {
    font-size: $font18;
    color: $black-medium;
    margin-bottom: 26px;
    @media (max-width: $mobile-max-width) {
      font-size: 1.125rem;
    }
    span {
      display: inline-block;
    }
  }
  &-line2 {
    font-size: $font18;
    color: $black-medium;
    margin-bottom: 40px;
    @media (max-width: $mobile-max-width) {
      font-size: 0.875rem;
    }
  }
  &-note {
    margin-top: 20px;
    margin-bottom: 40px;
    color: $black-medium;
    font-size: $font16;
    @media (max-width: $mobile-max-width) {
      font-size: 0.875rem;
    }
    &.summary-text-info {
      font-weight: 400;
      padding-right: 0;
      @media (max-width: 767px) {
        margin-bottom: 15px;
      }
    }
  }
  &-action {
    margin: 0 auto;
    text-align: center;
    width: 984px;
    padding-bottom: 100px;
    @media (max-width: $mobile-max-width) {
      width: 100%;
      margin: 0;
      padding-bottom: 30px;
    }
    .btn-mui {
      width: 200px;
      color: #4b4b4b;
      @media (max-width: 374px) {
        font-size: 14px;
        padding: 6px 5px;
      }
    }
  }
}

.label-hidden {
  height: 0;
  width: 0;
  z-index: -1;
  color: transparent !important;
  position: absolute;
  font-size: 0 !important;
}
.heading-hidden {
  z-index: -1;
  color: #000;
  background: #fff;
  position: absolute;
  left: -9999px;
}
.shipping-note {
  font-size: 13px;
  color: #4b4b4b;
  font-weight: 500;
}
.hero-banner {
  position: relative;
  z-index: 3;
  .user-name {
    .welcome-msg {
      font-family: "SignPainter-HouseScript" !important;
    }
    .name {
      font-family: "FrontageCondensed-Inline" !important;
    }
  }
}
.no-items {
  text-align: center;
  padding: 20px;
}
.IE-only {
  display: none !important;
  @media all and (-ms-high-contrast: none) {
    display: block !important;
  }
}
.out-of-stock {
  color: $color-white;
}

.profile-flexContainer button {
 color: #FFFFFF
}
.form-card-container {
  width: calc(100% + 30px) !important;
  margin: -15px !important;
}
.edit-shipping .form-card-container { padding: 30px 0 30px;}

.summary-col{
  padding: 0 !important;
}
.pt-5 {
  padding-top: 30px !important;
}
.mrt-5 { margin-top: 15px;}
.br-0 { border-right: 0 none !important;}
.form-card-container.change-password-lay {
  padding: 20px 0;
}

.color-title { color: #0289BA !important;}

.btn-continue-shopping { @include standardLinkNew($blue-dark, $white);}
.btn-shopping { text-decoration: none;}
.sticky-head{
  @include stickyHead;
  @include boxShadowContainer;
  &-inner{
    margin: 0 auto !important;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    max-width: $container-inner-width;
    margin-bottom: 30px;
    width: 100%;
    @media (max-width: 767px) {
      flex-flow: column;
      align-items: flex-start;
    }
  }
  button{
    @media (max-width: 767px) {
      margin-top: 10px !important;
    }
  }
  .btn-secondary{
    @include standardLinkNewReverse($white, $blue);
    font-size: $font16;
    min-width: 50px;
    margin-right: 15px;
    padding: 7px 20px;
  }
  .btn-primary{
    @include standardLinkNew($blue, $white);
    padding: 7px 20px;
    font-size: $font16;
    &[disabled] {
      background-color: $white;
      color: $black-light;
      &:hover,
      &:focus {
        background-color: $white;
        color: $black-light;
      }
    }
  }
  &.shop-by-category-page-heading{
    &+div{
      padding-top: 120px !important;
      @media (max-width: 767px) {
        padding-top: 180px !important;
      }
    }
  }
  .card-summary-page-heading-inner, .thankyou-page-heading-inner{
    min-height: auto;
    padding: 0;
    div{
      padding: 0;
    }
  }
  &+div{
    padding-top: 90px !important;
    @media (max-width: 767px) {
      padding-top: 80px !important;
    }
    &.mini-cart{
      padding-top: 0 !important;
      @media (max-width: 767px) {
        padding-top: 0 !important;
      } 
    }
  }
  &.has-button{
    &+div{
      @media (max-width: 767px) {
        padding-top: 125px !important;
      }
    }
    &>div{
      @media (max-width: 767px) {
        flex-flow: column;
      }
    }
    h1{
      @media (max-width: 767px) {
        width: 100%;
      }
    }
    .button-group{
      margin-top: 0;
      display: block;
      width: auto;
      @media (max-width: 767px) {
        width: 100%;
      }
    }
  }
  &.has-button-mobile{
    .button-group{
      .show-web{
        display: none !important;
        @media (max-width: 767px) {
          display: block !important;
        }
      }
    }
  }
  &.wizard-step{
    padding: 0;
    &+div {
      padding-top: 140px !important;
      @media (max-width: 991px) {
        padding-top: 135px !important;
      }
      @media (max-width: 767px) {
        padding-top: 110px !important;
      }
    }
  }
}
.sticky-title{
  @include stickyTitle;
  &-wrapper{
    display: flex;
    align-items: flex-end;
    @media (max-width: 991px) {
      flex-flow: column;
      align-items: flex-start;
    }
  }
}
.sticky-head-wrapper{
  &+div{
    padding-top: 75px !important;
    @media (max-width: 767px) {
      padding-top: 120px !important;
    }
  }
}
.has-toast{
  .sticky-head{
    top: 120px;
    @media (max-width: 767px) {
      top: 112px;
    }
    @media (max-width: 420px) {
      top: 105px;
    }
    &.wizard-step{
      &+div {
        padding-top: 152px !important;
        @media (max-width: 991px) {
          padding-top: 135px !important;
        }
        @media (max-width: 767px) {
          padding-top: 110px !important;
        }
      }
    }
  }
}
.show-web{
  @media (max-width: 767px) {
    display: none !important;
  }
}
.content-main{
  &-static{
    .container-root{
      @media (max-width: 767px) {
        margin-bottom: 0 !important;
      }
    }
  }
}