@import "../../styles/commonStyle/commonStyle.scss";
.multipack {
  &-modal{
    width: 850px;
    background: transparent linear-gradient(0deg, $purple-dark 0%, $purple 100%) 0% 0% no-repeat;
    color: $white;
    padding: 55px;
    @media (max-width: 991px) {
      padding: 40px;
    }
    @media (max-width: 767px) {
      padding: 30px;
    }
    @media (max-width: 359px) {
      padding: 30px 10px;
    }
    &>div{
      padding: 0 !important;
    }
    h1{
      font-size: $font48;
      color: $white;
      margin: 0 0 0 -5px;
      @media (max-width: 991px) {
        font-size: $font36;
        margin: 0;
      }
      @media (max-width: 767px) {
        font-size: $font28;
        text-align: center;
      }
    }
    h2{
      font-size: $font28;
      color: $white;
      font-family: "Poppins", sans-serif !important;
      margin: 10px 0 10px 0;
      @media (max-width: 767px) {
        font-size: $font18;
        text-align: center;
        margin: 0 0 10px 0;
      }
    }
    p{
      font-size: $font18;
      color: $white;
      padding-right: 20px;
      @media (max-width: 767px) {
        font-size: $font14;
        padding-right: 0;
        text-align: center;
      }
    }
    .btn{
      &-group{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-top: 40px;
        @media (max-width: 767px) {
          margin-top: 15px;
        }
      }
      &-primary{
        text-decoration: none;
        @include standardLinkNew($white, transparent);
        color: $purple-medium;
        width: calc(50% - 20px);
        &:hover{
          color: $white;
        }
        @media (max-width: 767px) {
          width: 100%;
          margin-bottom: 15px;
        }
      }
      &-secondary{
        text-decoration: none;
        @include standardLinkNewReverse(transparent, $white);
        width: calc(50% - 20px);
        &:hover{
          color: $purple-medium;
        }
        @media (max-width: 767px) {
          width: 100%;
        }
      }
    }
  }
  &-confirm{
    &-left{
      max-width: 340px !important;
      flex-basis: 340px !important;
      padding-right: 50px;
      text-align: center;
      @media (max-width: 991px) {
        max-width: 300px !important;
        flex-basis: 300px !important;
      }
      @media (max-width: 767px) {
        max-width: 100% !important;
        flex-basis: 100% !important;
        padding-right: 0;
        text-align: center;
        padding-bottom: 20px;
      }
      img{
        max-width: 100%;
        max-height: 220px;
        @media (max-width: 767px) {
          max-width: 200px;
          max-height: 200px;
        }
      }
    }
    &-right{
      max-width: calc(100% - 340px) !important;
      flex-basis: calc(100% - 340px) !important;
      @media (max-width: 991px) {
        max-width: calc(100% - 300px) !important;
        flex-basis: calc(100% - 300px) !important;
      }
      @media (max-width: 767px) {
        max-width: 100% !important;
        flex-basis: 100% !important;
      }
    }
  }
}
