/* google font */
@import url("https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700,800,900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap");

@font-face {
  font-family: "SignPainter-HouseScript";
  src: url("/www/fonts/SignPainter-HouseScript.eot");
  src: url("/www/fonts/SignPainter-HouseScript.eot?#iefix")
      format("embedded-opentype"),
    url("/www/fonts/SignPainter-HouseScript.svg#SignPainter-HouseScript")
      format("svg"),
    url("/www/fonts/SignPainter-HouseScript.ttf") format("truetype"),
    url("/www/fonts/SignPainter-HouseScript.woff") format("woff"),
    url("/www/fonts/SignPainter-HouseScript.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "FrontageCondensed-Inline";
  src: url("/www/fonts/FrontageCondensed-Inline.eot");
  src: url("/www/fonts/FrontageCondensed-Inline.eot?#iefix")
      format("embedded-opentype"),
    url("/www/fonts/FrontageCondensed-Inline.svg#FrontageCondensed-Inline")
      format("svg"),
    url("/www/fonts/FrontageCondensed-Inline.ttf") format("truetype"),
    url("/www/fonts/FrontageCondensed-Inline.woff") format("woff"),
    url("/www/fonts/FrontageCondensed-Inline.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "RailroadGothic";
  src: url("/www/fonts/OPTIRailroadGothic.eot");
  src: url("/www/fonts/OPTIRailroadGothic.eot?#iefix")
      format("embedded-opentype"),
    url("/www/fonts/OPTIRailroadGothic.svg#OPTIRailroadGothic") format("svg"),
    url("/www/fonts/OPTIRailroadGothic.ttf") format("truetype"),
    url("/www/fonts/OPTIRailroadGothic.woff") format("woff"),
    url("/www/fonts/OPTIRailroadGothic.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "GothamBold";
  src: url("/www/fonts/HomepageBaukasten-Bold.eot");
  src: url("/www/fonts/HomepageBaukasten-Bold.eot?#iefix")
      format("embedded-opentype"),
    url("/www/fonts/HomepageBaukasten-Bold.ttf") format("truetype"),
    url("/www/fonts/HomepageBaukasten-Bold.woff") format("woff"),
    url("/www/fonts/HomepageBaukasten-Bold.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}

body,
.form-label {
  font-family: "Poppins", sans-serif !important;
}

h1,
h2,
h3,
h4,
.hero-text .hero-header,
.home-box-wrapper .user-title,
.filter-title-mob {
  font-family: "RailroadGothic" !important;
  font-weight: normal;
}
