@import "../../styles/commonStyle/commonStyle.scss";
.multipack {
  &-product-tout {
    &.product-box-bundle-card {
      background: transparent
        linear-gradient(180deg, $purple 0%, $purple-dark 100%) 0% 0% no-repeat;
      text-align: center;
      &.isHoliday{
        background-color: transparent;
        background-image: url("/www/images/holiday-tout-bg.png");
        background-size: cover;
        .product-heading, 
        .product-brand,
        .selected-product{
          color: $blue;
        }
      }
      .product-content-wrapper {
        @media (max-width: 991px) {
          padding-left: 5px;
          padding-right: 5px;
        }
      }

      .product-heading {
        color: $white;
        min-height: auto;
        margin-bottom: 8px;
      }
      .product-brand {
        color: $white;
        font-size: 13px;
        line-height: 18px;
        @media (max-width: 1024px) {
          font-size: 12px;
        }
        @media (max-width: 991px) {
          font-size: 11px;
        }
        @media (max-width: 767px) {
          font-size: 9px;
          line-height: 15px;
        }
      }
      .product-action {
        margin-top: 5px;
      }
      .selected-product {
        color: $white;
      }
    }
  }
}
