@import "../../../styles/commonStyle/commonStyle.scss";
.multipack {
  @include maxContainer();
  background-image: none;
  @media (max-width: 767px) {
    background-image: none;
  }
  .container-root {
    max-width: 1220px !important;
  }
  &-body {
    padding-bottom: 50px;
    min-height: calc(100vh - 120px);
    &-left {
      max-width: calc(100% - 336px) !important;
      flex-basis: calc(100% - 336px) !important;
      @media (max-width: 991px) {
        max-width: 100% !important;
        flex-basis: 100% !important;
      }
      @media (max-width: 767px) {
        padding: 0 !important;
      }
    }
    &-right {
      max-width: 336px !important;
      flex-basis: 336px !important;
      @media (max-width: 991px) {
        max-width: 100% !important;
        flex-basis: 100% !important;
      }
    }
  }
}
.holiday-shop {
  @include holidayShopBg();
  .multipack {
    &-header {
      box-shadow: none;
      @include holidayShopBg();
      @media (max-width: 1024px) {
        padding-top: 50px;
      }
      &-img {
        height: 290px;
        margin-left: 0;
        position: relative;
        @media (max-width: 1024px) {
          height: 270px;
        }
        @media (max-width: 991px) {
          height: auto;
        }
        @media (max-width: 767px) {
          max-width: 120px !important;
          flex-basis: 120px !important;
        }
        .price-wrapper {
          display: none;
        }
        img {
          max-height: 260px;
          margin-left: 0;
        }
      }
      &-content {
        @media (max-width: 767px) {
          padding-right: 0;
        }
        h1 {
          color: $blue2;
          font-size: $font42;
          @media (max-width: 1366px) {
            font-size: $font36;
          }
          @media (max-width: 991px) {
            font-size: $font30;
          }
          @media (max-width: 767px) {
            font-size: $font28;
          }
          @media (max-width: 359px) {
            font-size: $font26;
          }
        }
        p {
          color: $blue2;
          @media (max-width: 767px) {
            font-size: 12px;
            position: relative;
            z-index: 2;
          }
          @media (max-width: 359px) {
            font-size: 10px;
          }
        }
        .search-bar {
          @media (max-width: 767px) {
            left: 0;
            width: 100%;
            padding-left: 40px;
            padding-right: 40px;
            background: $white;
          }
          div[class*="-underline"] {
            color: $blue2;
            &:before,
            &:after {
              border-color: $blue2;
            }
          }
        }
      }
      &.sticky-head {
        & + div {
          padding-top: 330px !important;
          @media (max-width: 1024px) {
            padding-top: 350px !important;
          }
          @media (max-width: 991px) {
            padding-top: 330px !important;
          }
        }
        &.smaller {
          @media (max-width: 1024px) {
            padding-top: 15px;
          }
          .multipack-header-content .search-bar {
            background-color: transparent;
            padding-left: 0;
            padding-right: 0;
          }
          & + div {
            padding-top: 210px !important;
            @media (max-width: 767px) {
              padding-top: 170px !important;
            }
          }
        }
      }
      &:not(.smaller) {
        &:before {
          @media (max-width: 1366px) {
            content: "";
            position: absolute;
            left: 0;
            bottom: 20px;
            width: 100%;
            height: 80px;
            background-color: $white;
          }
        }
      }
    }
    &-back {
      color: $blue2;
      margin-bottom: 0;
      position: absolute;
      top: 10px;
      left: 40px;
      z-index: 2;
      @media (max-width: 767px) {
        position: unset;
        top: 0;
        left: 0;
      }
    }
  }
  .lists {
    padding-bottom: 20px;
    min-height: calc(100vh - 300px);
    padding-top: 20px;
    @media (max-width: 1024px) {
      min-height: auto;
    }
  }
}
.loader-full {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(255, 255, 255, 0.5);
  height: 100%;
  & > div {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
