@import "./../../../styles/commonStyle/commonStyle.scss";
div.checkout-container {
  margin-top: 50px !important;
  // min-width: calc(90%) !important;
  // min-width: 90% !important;
  max-width: 1280px !important;
  @media (max-width: 767px) {
    margin-top: 30px !important;
  }
}
.summary-right {
  .card-summary-container {
    margin: 0 !important;
    padding: 30px 50px 40px 50px;
    @media (max-width: 1200px) {
      padding: 0 15px 15px 15px;
    }
    @media (max-width: 767px) {
      padding: 30px 20px 40px 20px;
    }
    .card-summary-header {
      padding-left: 0;
      padding-bottom: 0 !important;
    }
    .item-qty {
      margin-top: 15px;
      font-weight: 400;
      color: $black-light;
    }
  }
}
.summary-list {
  @include scrollbars(0.5em, $scroll-bar);
  height: 100%;
  max-height: 425px;
  overflow-y: auto;
  overflow-x: hidden;
  @media (max-width: $mobile-max-width) {
    max-height: 40vh;
  }
  @media (max-width: 767px) {
    max-height: 35vh;
  }
  img {
    max-width: 100%;
  }
}
.summary-list-grid {
  img {
    max-width: 100%;
  }
}
.products-total {
  margin-bottom: 30px;
  .h3 {
    font-size: 15px;
    font-weight: 500;
  }
}
.button-group {
  display: flex;
  margin-top: 35px;
  justify-content: center;
  @media (max-width: $mobile-max-width) {
    padding: 0;
  }
  @media (max-width: 767px) {
    justify-content: center;
    margin-top: 0 !important;
  }
  &-small {
    margin-top: 0px;
  }
  .btn {
    flex: 1;
    box-shadow: none;
    max-width: 100%;
    margin: 0 !important;
    &.Mui-disabled {
      background-color: $grey;
      color: $black;
      pointer-events: none;
    }
    &-secondary {
      @include standardLinkNewReverse($white, $blue);
    }
    &-primary {
      @include standardLinkNew($blue, $white);
      &.Mui-disabled {
        background-color: $grey;
        color: $black;
        pointer-events: none;
      }
      //margin-left: 15px !important;
    }
    &.btn-sm {
      font-size: $font14;
      min-width: 120px;
      flex: none;
    }
  }
}

.content-main {
  @include maxContainer;
}

.checkout-content-body-inner:not(.inner-view) > .form-card {
  padding-left: 50px !important;
  padding-right: 50px !important;
  @media (max-width: 991px) {
    padding-left: 15px !important;
    padding-right: 15px !important;
  }
}
