@import "./../../../styles/commonStyle/commonStyle.scss";
@mixin colorBoxPallete {
  &.brands,
  &.brand {
    background: transparent linear-gradient(299deg, #0943b9, #1aabbb) no-repeat;
    border: 1px solid #3d87e8;
  }

  &.snack-type {
    background: transparent linear-gradient(270deg, #b20c0f, #e70b21) no-repeat;
    border: 1px solid #ce0c19;
  }

  &.occasion {
    background: transparent linear-gradient(243deg, #51127f, #cd1383) no-repeat;
    border: 1px solid #ce0c19;
  }

  &.flavour,
  &.flavor {
    background: transparent linear-gradient(294deg, #facc2e, #f8451d) no-repeat;
    border: 1px solid #f83f1c4d;
  }
}
.shop-by-category {
  &-heading {
    .sub-heading {
      color: $black-light;
      font-size: $font14;
      margin: 0 0 0 10px;
      font-weight: 400;
      @media (max-width: 767px) {
        margin: 5px 0 0 2px;
      }
    }
    @media (max-width: 767px) {
      display: block;
    }
  }
  &-page {
    &-heading {
      &-inner {
        display: flex;
        margin: 0 auto;
        max-width: $container-inner-width;
        padding: 0 16px;
        width: 100%;
        align-content: space-around;
        align-items: center;
        @media (max-width: 767px) {
          display: block;
        }
      }
    }
  }
  &-container {
    display: block;
    width: 100%;
    @include maxContainer;
    min-height: calc(100vh - 160px);
    &-inner {
      max-width: $container-inner-width;
      padding: 50px 16px;
      flex-wrap: wrap;
      margin: 0 auto;
      display: flex;
      @media (max-width: 991px) {
        padding: 10px 16px 40px 16px;
      }
      button {
        span[class*="MuiTouchRipple"] {
          display: none;
        }
      }
    }
  }
  &-next {
    text-align: right;
    display: flex;
    @media (max-width: 767px) {
      width: 100%;
    }
    > span {
      display: none;
      font-size: $font14;
    }
  }
  &-category {
    display: block;
    width: 100%;
    &-item {
      position: relative;
      cursor: pointer;
      .main-image {
        display: block;
      }
      .main-hover-image {
        display: none;
      }
      &.selected {
        .main-image {
          display: none;
        }
        .main-hover-image {
          display: block;
        }
      }
    }

    img {
      width: 100%;
      margin: 0 auto;
    }

    .sub-heading {
      padding: 6px 16px;
      color: inherit;
      text-align: center;
      line-height: 1;
    }

    .main-category {
      .shop-by-category-category-item {
        background: transparent !important;
        width: 100%;
      }
    }

    .main-sub-category {
      margin-top: 40px !important;
      box-shadow: 0 0 0;
      background: transparent;

      &::before {
        display: none;
      }
      &:first-child {
        margin-top: 0 !important;
      }
      .cat-accordion-wrapper {
        padding-left: 2px;
        padding-right: 2px;
      }
      .shop-by-category-category-item {
        width: 100%;
        min-height: 120px;
        padding: 6px 16px;
        font-size: $font20;
        border: 1px solid #a2a2a2;
        background-color: $white;
        font-family: "RailroadGothic" !important;
        @media (max-width: 767px) {
          min-height: 90px;
          padding: 6px 10px;
          font-size: $font16;
        }
        .sub-main-image {
          display: block;
        }
        .sub-main-hover-image {
          display: none;
        }
        &.selected {
          .sub-main-image {
            display: none;
          }
          .sub-main-hover-image {
            display: block;
          }
        }
        &.brands,
        &.brand {
          min-height: 80px;
          color: #1a20b0;
          padding: 0;
          border: none;
          background: transparent;
          @media (max-width: 767px) {
            min-height: 50px;
          }
        }
        &.flavour,
        &.flavor {
          color: #f95e1f;
        }
        &.occasion {
          color: #991ab0;
        }
        &.snack-type {
          color: #d9192a;
        }
        &.selected {
          color: $white;
          @include colorBoxPallete;
          &.brands,
          &.brand {
            border: none;
            background: transparent;
          }
        }
      }
      .category-heading {
        &.category-heading {
          @include boxShadowContainer;
          background: linear-gradient($black-medium, $black);
          padding: 1px 15px 1px 30px;
          min-height: auto;
          h2 {
            margin: 0;
            font-family: "RailroadGothic";
            font-size: $font24;
            letter-spacing: 0.69px;
          }
          > div {
            margin: 0;
            padding: 0;
            color: $white;
            svg {
              font-size: $font30;
            }
          }
          @include colorBoxPallete;
        }
      }
    }

    .sub-category {
      > div {
        margin-top: 10px;
      }
    }
  }
}
