@import "../../../styles/commonStyle/commonStyle.scss";
.holiday {
  &-shop {
    &-slider {
      width: 360px;
      @media (max-width: 767px) {
        width: 100%;
      }
      .btn-link {
        margin: 10px auto 0 auto;
        display: block;
        font-size: $font16;
        &:hover {
          color: $blue;
        }
      }
      .image-gallery-slide {
        .image-gallery-image {
          width: auto;
          max-width: 100%;
          max-height: 300px;
        }
      }
      .image-gallery-thumbnails {
        padding-top: 34px;
      }
      .image-gallery-thumbnail {
        width: 70px;
        height: 66px;
        border: 1px solid #707070;
        cursor: pointer;
        position: relative;
        &:focus{
          border-color: $color-black !important;
          border-width: 2px;
          border-radius: 2px;
        }
        &:before {
          content: "";
          background: transparent
            linear-gradient(180deg, #8ecbf7 0%, #ffffff 100%) 0% 0%;
          height: 20px;
          width: 100%;
          position: absolute;
          top: 0;
          left: 0;
        }
        & + .image-gallery-thumbnail {
          margin-left: 5px;
        }
        &.active {
          border-width: 2px;
        }
        .image-gallery-thumbnail-image {
          width: auto;
          max-width: 65px;
          max-height: 60px;
        }
      }
      .image-gallery-icon {
        .image-gallery-svg {
          height: 60px;
          width: 20px;
        }
      }
    }
    &-content {
      padding-left: 50px;
      padding-right: 35px;
      @media (max-width: 991px) {
        padding-left: 20px;
        padding-right: 0;
      }
      @media (max-width: 767px) {
        padding-left: 0;
      }
      .out-of-stock {
        color: #c04319;
        text-transform: lowercase;
        text-align: left;
        margin-top: 10px;
        cursor: default;
        font-size: $font24;
        font-weight: 600;
      }
      h1 {
        font-family: "Poppins", sans-serif !important;
        color: $blue2;
        font-size: $font26;
        @media (max-width: 767px) {
          font-size: $font20;
        }
      }
      p {
        color: $blue2;
        font-size: $font14;
      }
      .size-dropdown-root {
        width: 100%;
        @media (max-width: 767px) {
          width: calc(100% - 25px);
        }
        @media (max-width: 359px) {
          width: calc(100% - 10px);
        }
        &:after {
          content: "\f107";
          font-weight: 700;
          font-family: "Font Awesome\ 5 Free", sans-serif;
          color: $blue2;
          font-size: 30px;
          position: absolute;
          top: 50%;
          right: 10px;
          transform: translateY(-50%);
          pointer-events: none;
          -webkit-text-stroke: 2.5px #fff;
          @media (max-width: 767px) {
            font-size: 22px;
            right: 8px;
            -webkit-text-stroke: 2px #fff;
          }
        }
        label {
          position: absolute;
          left: -9999px;
          z-index: 0;
          & + div {
            background-color: transparent;
            &:before,
            &:after {
              display: none;
            }
          }
        }
        select {
          padding: 16px 30px 16px 8px;
          border: 1px solid $blue2;
          border-radius: 8px;
          color: $black-light;
          font-size: $font16;
          font-family: "Poppins", sans-serif;
          @media (max-width: 767px) {
            font-size: 13px;
            padding: 10px 20px 10px 7px;
            height: auto;
          }
          @media (max-width: 359px) {
            font-size: 11px;
          }
          &:focus{
            outline: -webkit-focus-ring-color auto 2px !important;
            outline-color: $color-black !important;
            border-radius: 2px;
          }
        }
        svg {
          opacity: 0;
        }
      }
      .quantity-count-contain {
        justify-content: flex-end;
        @media (max-width: 767px) {
          margin-top: 0;
        }
      }
      .quantity-count-wrapper {
        width: 110px;
        @media (max-width: 767px) {
          width: 100px;
        }
        input {
          color: $black-light;
        }
        button {
          color: $black-light;
          border-color: $black-light;
          height: 30px;
          width: 30px;
          font-size: 15px;
        }
        .size-wrapper {
          color: $black-light;
        }
      }
    }
    &-modal {
      width: 900px !important;
      padding: 80px 60px 50px 60px;
      background: url(/www/images/holiday-popup-bg.png);
      background-repeat: no-repeat;
      background-size: 220%;
      background-position: -5px -60px;
      @media (max-width: 991px) {
        padding: 80px 40px 30px 40px;
      }
      @media (max-width: 767px) {
        padding: 20px 15px 15px 15px;
        max-width: calc(100% - 45px) !important;
        margin: 45px 15px !important;
        background-size: 850px;
        background-position: -130px -20px;
      }
      & > div {
        padding: 0 !important;
        overflow: hidden;
      }
      .icn-close {
        padding: 0 !important;
        min-width: auto !important;
        box-shadow: 0px 2px 4px #00000080;
        border-radius: 50% !important;
        position: absolute !important;
        background: $white !important;
        top: -15px;
        z-index: 22;
        right: 25px;
        min-height: 30px;
        min-width: 30px;
        i {
          color: #262626;
          width: 30px;
          height: 30px;
          position: relative;
          border-radius: 50%;
          font-size: 18px;
          &:before {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            -webkit-text-stroke: 2px #fff;
            padding-left: 1px;
          }
        }
        @media (max-width: $mobile-max-width) {
          top: -10px;
        }
        @media (max-width: 767px) {
          right: 5px;
        }
      }
    }
    &-size {
      margin-top: 20px;
      margin-bottom: 40px;
      @media (max-width: 767px) {
        align-items: center;
      }
      & > div {
        &:first-child {
          max-width: calc(100% - 140px);
          @media (max-width: 767px) {
            flex: 1;
            max-width: 100%;
          }
        }
        &:last-child {
          @media (max-width: 767px) {
            max-width: none;
            flex: none;
          }
        }
      }
    }
  }
}
div.holiday {
  &-accordion {
    &-wrapper {
      box-shadow: none;
      margin: 0;
      background-color: transparent;
    }
    &-header {
      border: none;
      padding: 0;
      min-height: auto;
      justify-content: flex-start;
      &.Mui-expanded {
        min-height: auto;
      }
      & > div {
        flex-grow: unset;
        padding-top: 0;
        padding-bottom: 0;
        &.Mui-expanded {
          margin: 0;
        }
      }
      h2 {
        font-family: "Poppins", sans-serif !important;
        font-size: $font20;
        text-transform: uppercase;
        color: $blue2;
        font-weight: 400;
      }
      .expand-more-icon {
        color: $blue2;
        font-size: 40px;
      }
    }
    &-body {
      padding: 10px 0;
    }
  }
}
.modal-size-chart {
  padding: 0;
  width: 620px;
  max-width: calc(100% - 30px) !important;
  margin: 10px auto !important;
  & > div {
    padding: 0 !important;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  img {
    max-width: 100%;
  }
}
