@import "./../../../styles/commonStyle/commonStyle.scss";

.page {
  &-product-listing {
    @include maxContainer;
    position: relative;
    min-height: calc(100vh - 130px);
    .container-root {
      max-width: $container-inner-width !important;
    }
    .product-grid-container {
      padding-bottom: 50px;
    }
  }
  &-heading {
    @include pageHeading;
  }
}
