@import "./../../styles/commonStyle/commonStyle.scss";
.product-box {
  &-bundle-card {
    border-radius: 8px !important;
    background: $color-white 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px $color-box-shadow !important;
    border: 1px solid $color-border;
    width: 100%;
    outline: 0;
    height: 350px;
    opacity: 1;
    position: relative;

    @media (max-width: 767px) {
      height: 300px;
    }
    img {
      max-height: 150px;
      object-fit: contain;
    }

    .product-content-wrapper {
      cursor: pointer;
      display: block;
      padding: 5px 12px;
      width: 100%;
      height: 100%;

      @media (max-width: 1025px) {
        cursor: default !important;
      }
      @media (max-width: 767px) {
        padding: 10px;
      }
      @media (max-width: 360px) {
        padding: 10px 8px;
      }
      @media (max-width: 359px) {
        padding: 10px 12px;
      }
    }
    &:hover {
      @media (min-width: 1025px) {
        transition: transform 0.5s !important;
      }
    }
    &.horizontal-bundle-card {
      height: auto;
      box-shadow: 0 0 0 !important;
      border: 0;
      border-radius: 0 !important;
      display: flex;
      .product-content-wrapper {
        padding: 10px 0;
        display: flex;
      }
      &.inactive {
        background: transparent !important;
      }
      .product-information {
        display: inline-block;
        padding: 5px;
        width: 100%;
      }
      .product-heading {
        display: inline-block;
        color: $black-medium;
        font-weight: 500;
        span {
          white-space: unset;
        }
      }
      .out-of-stock {
        &.selected-product {
          font-size: $font16;
          text-transform: lowercase;
          text-align: left;
          margin-top: 10px;
        }
      }
      .product-action {
        width: auto;
        display: flex;
        justify-content: space-between;
        margin-top: 10px;
        text-align: left;
        .selected-product {
          font-size: $font16;
          text-align: left;
          text-transform: capitalize;
        }
        .price {
          color: $black-medium;
          font-weight: 500;
        }
      }

      .media-card-wrapper {
        height: auto;
        margin: 5px 0;
        max-width: 80px;
        img {
          padding: 0 5px;
        }
      }
    }

    &.inactive {
      background: $white !important;
    }
    &.active {
      overflow: unset;
      background-color: $black-light;
      .product-information {
        height: 100%;
        display: flex;
        align-items: center;
        max-height: 170px;
        margin-top: -20px;
        &.show-scroll {
          overflow-y: auto;
          @include scrollbars(0.25em, $scroll-bar);
          align-items: flex-start;
        }
        &.align-top {
          align-items: flex-start;
        }
        @media (max-width: 991px) {
          max-height: 140px;
          margin-top: 10px;
        }
      }
      .more-vert,
      .product-brand,
      .product-heading,
      .bundle-card-info,
      .quantity-count-wrapper input {
        color: $white;
      }
      .media-card-wrapper {
        position: relative;
        transform: translateY(-40px);
        transition: all 0.3s ease-out;
        margin-bottom: 15px;
        @media (max-width: $mobile-max-width) {
          transform: none;
          height: 135px;
        }
        @media (max-width: 767px) {
          margin-bottom: 10px;
          height: 110px;
        }
        img {
          @media (min-width: 991px) {
            transform: scale(0.98);
            transition: all 0.3s ease-out;
          }
        }
      }
      .shadow {
        position: absolute;
        width: 100%;
        height: 54px;
        left: 0;
        top: 158px;
        background: radial-gradient(
          ellipse at 50% 33%,
          rgba(0, 0, 0, 0.3),
          rgba(97, 97, 97, 0) 40%
        );
        @media (max-width: $mobile-max-width) {
          display: none;
          top: 138px;
        }
        @media (max-width: 767px) {
          height: 34px;
          top: 106px;
        }
      }

      &.holiday-shop-bundle {
        .shadow {
          top: 130px;
        }
        .product-information {
          margin-top: -30px;
          max-height: 156px;
          flex-direction: column;
          .quantity-box-size-information {
            width: 100%;
            margin-bottom: 10px;
            flex-direction: row-reverse;
          }
          .sizes-arrow-down {
            bottom: 5px;
          }
          .quantity-box {
            .quantity-count-wrapper {
              padding-right: 4px;
            }
            &.add-scroll {
              overflow-y: auto;
              @include scrollbars(0.25em, $scroll-bar);
              align-items: flex-start;
            }
          }
        }
        .quantity-box {
          max-height: 156px;
          overflow: hidden;
          &-size-information {
            color: $white;
            display: flex;
            justify-content: space-between;
            .size-chart {
              padding: 10px 0;
              color: $white;
              text-decoration: underline;
              text-transform: capitalize;
              font-size: $font14;
              font-weight: 400;
            }
            .all-sizes {
              padding: 10px 0;
              font-size: $font14;
              font-weight: 400;
            }
          }
          .size-wrapper .size {
            width: auto;
          }
        }
      }
    }
    .star {
      color: $color-yellow-hover;
    }
    .more-vert {
      color: $color-primary;
      &:focus{
        outline: -webkit-focus-ring-color auto 2px !important;
        outline-color: $color-black !important;
        border-radius: 2px;
      }
    }
    .product-options {
      position: absolute;
      top: 2px;
      left: 1px;
      width: calc(100% - 2px);
      z-index: 1;
      button {
        padding: 5px;
        svg {
          width: 30px;
          height: 30px;
          @media (max-width: 767px) {
            width: 24px;
            height: 24px;
          }
        }
      }
    }
    .product-brand {
      font-size: $font14;
      color: $color-grey-dark;
      margin-bottom: 5px;
      @media (max-width: 375px) {
        font-size: 11px;
      }
      @media (max-width: 374px) {
        font-size: 10px;
      }
      @media (max-width: 359px) {
        font-size: $font14;
      }
    }
    .product-heading {
      display: block;
      width: 100%;
      position: relative;
      margin-bottom: 5px;
      min-height: 40px;
      font-size: $font16;
      line-height: $font20;
      color: $color-grey-dark;
      span {
        display: inline-block;
        white-space: nowrap;
      }
      @media (max-width: 1024px) {
        font-size: $font14;
      }
      @media (max-width: 991px) {
        font-size: $font16;
      }
    }
    .product-action {
      text-align: center;
      margin-top: 15px;

      @media (max-width: 767px) {
        margin-top: 5px;
      }
    }
    .bundle-card-info {
      font-size: $font16;
      font-weight: 500;
      line-height: $font14;
      padding: 0;
      margin-bottom: 20px;
      color: #4b4b4b;
    }
    .out-of-stock {
      &.selected-product {
        padding-top: 4px;
        padding-bottom: 4px;
        text-align: center;
        display: block;
        cursor: default;
        margin-top: 16px;

        @media (max-width: 767px) {
          margin-top: 5px;
        }
      }
    }
    .media-card-wrapper {
      padding: 0 !important;
      height: 160px;
      position: relative;
      display: block;
      margin: 10px 0 20px 15px;
      background: transparent;
      width: calc(100% - 30px);
      @media (max-width: $mobile-max-width) {
        height: 140px;
        margin: 25px 0 15px 15px;
      }
      @media (max-width: 767px) {
        height: 110px;
        width: 100%;
        margin: 20px 0 10px 0;
      }
      &:hover {
        background: transparent;
      }
      span[class*="MuiTouchRipple"] {
        display: none;
      }
      img {
        padding: 0 10px;
        position: absolute;
        width: 100%;
        height: auto;
        top: 0;
        left: 0;
        max-height: 160px;
        font-size: $font14;
        line-height: $font16;
        @media (max-width: 991px) {
          max-height: 130px;
        }
        @media (max-width: 767px) {
          max-height: 100%;
        }
      }
    }
    .selected-product {
      color: #c04319;
      padding: 0;
      font-size: $font24;
      font-weight: 600;
      text-transform: lowercase;
      display: inline-block;
      &:hover {
        background: none;
      }
      &.out-of-stock {
        font-size: $font22;
      }
    }
  }
}
.quantity-box {
  height: auto;
  position: relative;
  margin-top: -10px;
  width: 100%;
  .size-wrapper {
    display: flex;
    align-items: flex-start;
    button .qty-icon {
      font-size: $font24;
    }
    .price {
      font-size: $font14;
      width: 60px;
      text-align: center;
      @media (max-width: 1025px) {
        width: 50px;
      }
      @media (max-width: 420px) {
        width: 40px;
      }
      span {
        font-size: $font12;
      }
    }
    .size {
      font-size: $font14;
      width: 80px;
      @media (max-width: 1025px) {
        width: 74px;
        font-size: $font14;
      }
      @media (max-width: 767px) {
        width: 48px;
      }
    }
  }
  .quantity-count-wrapper {
    button {
      @media (max-width: 1025px) {
        width: 22px;
        height: 22px;
      }
      @media (max-width: 376px) {
        width: 20px;
        height: 20px;
        font-size: 11px;
        padding-bottom: 2px;
      }
    }
  }
  .quantity-count-contain {
    align-items: flex-start;
    margin-top: 5px;
    padding-top: 5px;
  }
  .quantity-count-wrapper {
    input {
      width: 30px;
      @media (max-width: 376px) {
        font-size: $font14;
        width: 24px;
      }
    }
  }
}

.sizes-arrow {
  &-down {
    position: absolute;
    bottom: 0px;
    left: 50%;
    transform: translateX(-50%);
    color: $white;
    @media (max-width: 767px) {
      bottom: 8px;
    }
  }
}

div.product-grid-container {
  &.lists {
    .btn-mui {
      padding: 6px 16px;
      text-align: center;
      text-decoration: none;
      cursor: pointer;
      &[disabled] {
        cursor: default;
      }
      &.btn-red {
        &[disabled] {
          color: $color-red !important;
          pointer-events: none;
          &:hover {
            color: $color-red !important;
          }
        }
      }
    }
  }
  & > div {
    outline: 0;
    @media (max-width: 991px) {
      max-width: 33.33%;
      flex-basis: 33.33%;
    }
    @media (max-width: 767px) {
      max-width: 50%;
      flex-basis: 50%;
      padding: 4px !important;
    }
    @media (max-width: 376px) {
      padding: 2px !important;
    }
    @media (max-width: 359px) {
      max-width: 100%;
      flex-basis: 100%;
    }
    &.noproducts-grid {
      max-width: 100%;
      flex-basis: 100%;
    }
  }
}
