@import "../../styles/commonStyle/commonStyle.scss";
.multipack {
  &-cart {
    border: 1px solid #C7C7C7;
    border-radius: 10px;
    margin-top: 72px;
    @media (max-width: 991px) {
      width: 100%;
      padding: 0 !important;
      position: fixed;
      bottom: 0;
      left: 0;
      z-index: 5;
      flex-flow: column-reverse;
      border: none;
    }
    &.fixed{
      position: fixed;
      width: 318px;
      top: 100px;
      @media (max-width: 991px) {
        width: 100%;
        top: auto;
        z-index: 5;
        bottom: 0;
      }
    }
    &.open{
      @media (max-width: 991px) {
        background-color: rgba(0,0,0,.5);
        height: calc(100vh - 60px);
        justify-content: flex-start !important;
        padding-top: 10px !important; 
      }
      .multipack-cart-body{
        @media (max-width: 991px) {
          display: block;
        }
      }
    }
    &-header {
      background: transparent linear-gradient(180deg, $purple 0%, $purple-dark 100%) 0% 0% no-repeat;
      border-radius: 10px 10px 0 0;
      box-shadow: 0px -2px 5px #00000040;
      padding: 14px 5px 14px 30px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      color: $white;
      width: 100%;
      @media (max-width: 991px) {
        border-radius: 0;
        flex-wrap: wrap;
        padding: 12px 30px 8px 30px;
        flex-basis: auto !important;
      }
      .cart-button {
        @include standardLinkNew($white, $white);
        min-width: 100%;
        margin: 0;
        width: 100%;
        display: none;
        color: $purple-dark;
        @media (max-width: 991px) {
          display: block;
          padding: 8px 15px;
          margin-bottom: 5px;
        }
        &[disabled] {
          background-color: #d5d5d5;
          color: #9a9a9a;
          border-color: #d5d5d5;
        }
      }
      .title {
        font-size: $font20;
        @media (max-width: 991px){
          padding-left: 20px;
        }
        span{
          font-weight: 600;
        }
      }
    }
    &-arrow {
      min-width: auto;
      @media (max-width: 991px) {  
        padding: 0 !important;
      }
      svg{
        font-size: 40px;
        color: $white;
      }
    }
    &-body{
      padding: 15px 16px 25px 16px;
      background-color: $white;
      width: 100%;
      border-radius: 0 0 10px 10px;
      position: relative;
      @media (max-width: 991px) {  
        width: calc(100% - 30px);
        margin: 0 auto !important;
        border-radius: 6px 6px 0 0;
        display: none;
        flex-basis: auto !important;
      }
      .note {
        color: #676767;
        font-size: $font18;
        text-align: center;
        margin-top: 15px;
        @media (max-width: 991px) {
          width: 100%;
          padding: 0 15px;
        }
      }
    }
    &-content{
      min-height: 80px;
      max-height: calc(100vh - 500px);
      @include scrollbars(4px, #ADADAD, #D6D6D6);
      overflow-y: auto;
      @media (max-width: 991px) {
        max-height: calc(100vh - 360px);
        min-height: auto;
      }
      &>div{
        width: calc(100% - 10px);
        &:not(:last-child){
          border-bottom: 1px solid #CECECE;
        }
      }
    }
    &-action{
      text-align: center;
      margin: 20px 0 5px 0;
      @media (max-width: 991px) {
        display: none;
      }
      // &:hover{
      //   .warning-mincount{
      //     display: block;
      //   }
      // }
      .cart-button {
        @include standardLinkNew($purple-dark, $white);
        min-width: 100%;
        margin: 0;
        &[disabled] {
          background-color: $grey;
          color: $black;
          border-color: $blue;
        }
      }
    }
    .multipack-tout{
      box-shadow: none !important;
      .quantity-count-wrapper{
        justify-content: flex-start;
        button{
          height: 30px;
          width: 30px;
        }
      }
      &.product-box-bundle-card{
        .product-content-wrapper{
          padding: 10px 0;
        }
        .media-card-wrapper{
          width: 106px;
          height: auto;
          margin: 0;
          padding-right: 15px !important;
          img{
            padding: 0 15px 0 0;
          }
        }
        .product-information{
          width: calc(100% - 106px);
        }
        .product-heading{
          color: $black;
        }
        .product-action{
          margin-top: 5px;
          .quantity-count-contain{
            margin-top: 0;
          }
        }
      }
    }
  }
}
.warning-mincount{
  box-shadow: 0px 3px 9px #00000029;
  background-color: #5D1783;
  text-align: center;
  width: 230px;
  padding: 20px 20px;
  border-radius: 15px;
  position: absolute;
  bottom: -120px;
  left: -10px;
  //display: none;
  &-close{
    position: absolute !important;
    top: -15px;
    left: 40px;
    background-color: $white !important;
    padding: 5px !important;
    box-shadow: 0px 2px 8px #000;
    svg{
      color: $black;
    }
  }
  p{
    color: #fff;
    font-size: $font16;
    margin: 0;
  }
}