@import "../../../styles/commonStyle/commonStyle.scss";

.account {
  @include maxContainer;
  display: flex;
  flex-direction: column;
  &-info {
    background-color: rgba(196, 196, 196, 0.24);
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    text-align: center;
    padding: 30px 15px;
    h2 {
      font-size: 1.4em;
      font-weight: 500;
      color: #4b4b4b;
      @media (max-width: 767px) {
        font-size: 1.2em;
      }
    }
    h3 {
      font-size: 1.375em;
      font-weight: 300;
      color: #4b4b4b;
      margin-top: 8px;
      @media (max-width: 767px) {
        font-size: 1.125em;
      }
    }
  }
  &-form {
    @include boxShadowContainer;
    padding: 50px 100px !important;
    margin: 50px auto !important;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 70%;
    @media (max-width: 767px) {
      width: 100%;
      padding: 30px 15px !important;
      margin-top: 20px !important;
    }
    .form-card {
      .container {
        width: 70%;
        @media (max-width: 1200px) {
          width: 100%;
        }
        @media (max-width: 767px) {
          width: calc(100% - 20px) !important;
        }
        @media (max-width: 600px) {
          width: 100% !important;
          margin: 0;
        }
      }
      border: none;
      .button-group {
        display: flex;
        .done-btn {
          @include standardLinkNew($blue-dark, $white);
        }
        @media (max-width: 600px) {
          flex-wrap: wrap;
          flex-direction: column-reverse;
        }
      }
      .page-heading {
        @include pageHeading;
      }
      .h5 {
        font-size: $font18;
        color: $black;
        font-weight: 500;
      }
      .h2 {
        font-size: $font24;
        color: $black;
        font-weight: 700;
        @media (max-width: 600px) {
          line-height: 26px;
        }
      }

      .h6 {
        font-size: $font16;
        font-weight: 600;
        color: $black;
        margin-top: 35px;
      }
      p {
        color: $black;
        margin-top: 8px;
        margin-bottom: 40px;
        display: flex;
        align-items: center;
        font-size: $font18;
        .btn-link {
          color: $black;
          border: none;
          padding: 0;
          margin-left: 5px;
          font-weight: 500;
          font-size: $font18;
          @media (max-width: 600px) {
            @include standardLinkNew($blue-dark, $white);
            text-decoration: none;
            width: 100%;
            margin-top: 15px;
            padding: 5px;
          }
        }
        @media (max-width: 600px) {
          font-size: $font18;
          flex-wrap: wrap;
          border: 1px solid rgba(171, 171, 171, 0.42);
          border-radius: 4px;
          padding: 15px;
          margin: 20px 0;
        }
      }
      .tnc {
        margin-top: 20px;
        padding-top: 20px;
        font-size: $font16;
        border-top: 1px solid rgba(172, 172, 172, 0.31);
        p {
          color: $black-light;
          margin-top: 5px;
          margin-bottom: 40px;
          display: block;
          font-size: $font16;
          a {
            color: $black-light;
          }
        }
      }
      small {
        font-size: $font16;
        color: $black-medium;
      }
      ul{
        padding-left: 15px;
        li{
          margin-left: 17px;
        }
      }
    }
    .button-group {
      display: flex;
      justify-content: flex-end;
      button {
        font-family: "Poppins", sans-serif !important;
        &.btn {
          &-cancel {
            @include standardLinkNewReverse($white, $blue);
            min-width: 140px;
            @media (max-width: 600px) {
              min-width: 100%;
            }
          }
          &-create-account {
            @include standardLinkNew($blue, $white);
            flex: 1;
            margin-left: 15px;
            &.Mui-disabled {
              background-color: $grey;
              color: $black;
              pointer-events: none;
            }
            @media (max-width: 600px) {
              min-width: 100%;
              margin: 0 0 20px;
            }
          }
        }
      }
      .separator {
        @include verticalSeparator($orange);
        margin-top: 6px;
      }
    }
  }
}
