@import "./../../../styles/commonStyle/commonStyle.scss";
.card-summary {
  &-page-heading {
    background-color: $white;
    @include boxShadowContainer;
    &-inner {
      .page-heading {
        @include pageHeading;
        @media (max-width: 767px) {
          font-size: $font24 !important;
        }
      }
    }
    .button-group {
      display: flex;
      justify-content: flex-end;
      .disable-btn {
        background-color: $grey;
        color: $black;
        pointer-events: none;
      }
    }
    .page-heading {
      @include pageHeading;
    }
    .btn {
      &.Mui-disabled {
        background-color: $grey;
        color: $black;
        pointer-events: none;
      }
      &-secondary {
        @include standardLinkNewReverse($white, $blue);
      }
      &-primary {
        @include standardLinkNew($blue, $white);
        margin-left: 10px;
      }
    }
    .checkout-btn {
      @include standardLinkNew($blue, $white);
    }
    .separator {
      @include verticalSeparator($orange);
    }
  }
  &-page-footer {
    &-inner {
      margin: 0 auto;
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      max-width: $container-inner-width;
      padding-bottom: 20px;
    }
    .button-group {
      display: flex;
      justify-content: flex-end;
      width: 100%;
      margin-top: 50px;
      @media (max-width: 767px) {
        flex-wrap: wrap;
      }
      .btn-secondary{
        margin-right: 18px;
        @media (max-width: 767px) {
          margin-right: 0;
          margin-top: 15px;
        }
      }
      .disable-btn {
        background-color: $grey;
        color: $black;
        pointer-events: none;
      }
    }
    .continue-shopping-btn {
      @include standardLinkNewReverse($white, $blue);
      margin-right: 18px;
      @media (max-width: 767px) {
        margin: 20px 2px 15px 2px;
      }
    }
    .checkout-btn {
      @include standardLinkNew($blue, $white);
    }
    .separator {
      @include verticalSeparator($orange);
    }
  }
  &-wrapper {
    margin: 0 auto !important;
    max-width: $container-main-inner-width;
    padding: 0 16px;
    width: 100% !important;
    @media (max-width: 767px) {
      padding: 0 5px;
    }
    .horizontal-product-list {
      @media (max-width: 767px) {
        display: none;
      }
      background: $orange-lighter;
      .popular-products {
        padding: 0;
        .main-heading {
          margin: 15px 0;
        }
        .show-more-link {
          font-weight: 600;
          cursor: pointer;
          @include pageContent($orange);
          text-transform: uppercase;
          text-decoration: none;
        }
      }
    }
    .card-summary-header{
      padding-bottom: 10px;
    }
    .card-summary-content .bundle-card-summary{
      @media (max-width: 767px) {
        padding-top: 0;
        padding-bottom: 0;
      }
    }
    & > div {
      @media (max-width: 991px) {
        max-width: 100%;
        flex-basis: 100%;
      }
    }
  }

  &-container {
    @include boxShadowContainer;
    padding: 20px;
    margin: 20px auto;
  }

  &-header {
    justify-content: space-between;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    padding: 20px;
    @media (max-width: 767px) {
      padding: 20px 0;
    }
    h2, h3 {
      margin: 0;
      font-weight: 600;
      @include pageContent($black-light);
      font-family: "Poppins", sans-serif !important;
    }
    .clear-cart {
      font-weight: 600;
      cursor: pointer;
      @include pageContent($orange);
      background: transparent !important;
      border: 0;
      font-family: "Poppins", sans-serif !important;
    }
  }
  &-bottom-row {
    padding: 5px 0 5px 0;
    .h3 {
      font-size: 15px;
      font-weight: 500;
    }
    .summary-subtotal-value {
      text-align: end;
    }
    .summary-shipping-value {
      font-size: 15px;
      text-align: end;
    }
    .summary-tax {
      font-size: 15px;
    }
    .summary-shipping {
      font-size: 15px;
    }
  }

  &-content {
    .card-no-item {
      padding: 20px;
    }
    .bundle-card-summary {
      display: flex;
      align-items: flex-start;
      padding: 0 20px;
      margin-bottom: 20px;
      justify-content: space-between;
      position: relative;
      &:nth-last-child(2){
        &:after{
          display: none;
        }
      }
      &:after{
        content: '';
        position: absolute;
        bottom: 0;
        left: 20px;
        width: calc(100% - 40px);
        height: 1px;
        background: #e8e8e8;
        @media (max-width: 767px) {
          width: 100%;
          left: 0;
        }  
      }
      @media (max-width: 767px) {
        padding: 5px 0 5px;
      }
      .cart-summary-error{
        margin-bottom: 10px;
      }
      .summary-img {
        padding: 0 20px 0 0;
        @media (max-width: 767px) {
          padding: 10px 20px 0 0;
        }
        img {
          max-width: 100%;
        }
      }
      .btn-trash {
        padding: 5px;
        margin-top: 3px;
        color: $black-light;
        text-align: center;
        font-size: $font20;
        @media (max-width: 767px) {
          font-size: $font18;
          margin-top: 0;
        }
      }
      .summary-row-info {
        width: 100%;
        margin: 0;
        .trash-icon-btn {
          display: none;
        }
      }
      .title-product {
        @include pageContent($black-medium);
        font-weight: 500;
        @media (max-width: 767px) {
          font-weight: 600;
          font-size: $font14;
        }
      }
      .price {
        font-size: $font18;
        font-weight: 500;
        text-align: right;
        padding-top: 5px;
        @media (max-width: 767px) {
          font-size: $font17;
          padding-top: 0;
        }
        i {
          font-style: normal;
          vertical-align: super;
          font-size: 11px;
          position: relative;
          top: 2px;
        }
      }
    }
    .quantity-count-contain {
      margin-top: 0;
      justify-content: center;
      .quantity-count-wrapper {
        width: 110px;
        @media (max-width: 767px) {
          width: 90px;
        }
      }
      button {
        color: #6c6c6c;
        border-color: $blue-dark;
        padding: 0;
        height: 36px;
        width: 36px;
        border-width: 2px;
        @media (max-width: 767px) {
          height: 24px;
          width: 24px;
          padding-left: 1px;
          border-width: 1px;
        }
      }
      input {
        color: $black-light;
        pointer-events: none;
        width: 35px;
      }
    }
  }

  &-final {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    padding-left: 20px;
    padding-right: 25px;
    @media (max-width: 767px) {
      flex-wrap: wrap;
      padding-left: 15px;
      padding-right: 15px;
      .card-summary-total {
        text-align: right;
        width: 100%;
        margin-bottom: 15px;
      }
      .card-summary-information {
        order: 2;
      }
    }
  }
  &-information {
    font-size: $font14;
    span {
      display: block;
      color: $blue-dark;
    }
  }
  &-total {
    .title {
      font-size: $font24;
      color: $black-light;
      font-weight: 600;
      letter-spacing: 0.5px;
      @media (max-width: 767px) {
        font-size: $font16;
      }
    }
    .amount {
      font-size: $font22;
      color: $blue-dark;
      font-weight: 600;
      letter-spacing: 0.4px;
      @media (max-width: 767px) {
        font-size: $font16;
      }
      i {
        font-style: normal;
        vertical-align: super;
        font-size: 14px;
        position: relative;
        top: 2px;
      }
    }
  }
  &-notes {
    padding: 10px 5px;
    width: 100%;

    .thankyou-note {
      @include pageContent($black-light);
      font-size: $font14;
      margin-bottom: 0;
    }
    .summary-text-info {
      font-size: $font14;
      color: $blue-dark;
    }
  }
}

.card-summary {
  @include maxContainer;
  // background: url("/images/thankyou-cartreview.png") no-repeat top center;
  background-size: 100% auto;
  &-popular {
    @media (max-width: 991px) {
      display: none;
    }
  }
}
@media (max-width: 767px) {
  .cart-delete {
    order: 2;
  }
}

.coupon{
  &-code {
    width: 180px;
    margin-left: 18px !important;
    margin-right: 18px !important;
    @media (max-width: 767px) {
      width: 100%;
      margin-left: 0 !important;
      margin-right: 0 !important;
    }
    label{
      transform: translate(14px, 16px) scale(1);
      color: $black-light;
      font-weight: 500;
      font-family: "Poppins", sans-serif;
      &[class*="-shrink"]{
        color: $blue-dark;
        font-weight: 600;
      }
    }
    fieldset{
      border-radius: 8px;
    }
    input{
      padding-top: 0;
      padding-bottom: 0;
      color: $black-light;
      font-size: $font16;
      height: 48px;
    }
  }
  &-error{
    color: $color-red;
    font-size: $font14;
    display: flex;
    width: calc(100% - 355px);
    justify-content: flex-start;
    margin-top: 8px;
    @media (max-width: 1200px) {
      width: 300px;
      margin-top: 8px;
      
    }
    @media (max-width: 767px) {
      width: 100%;
      margin-top: 8px;
      
    }
  }
  &-buttons{
    padding-right: 35px;
    padding-left: 20px;
    min-width: 300px;
    text-align: right;
    @media (max-width: 767px) {
      padding-right: 0;
      padding-left: 0;
      min-width: auto;
      text-align: left;
    }
    .btn-link{
      @include standardLink($orange, $orange-dark);
      font-size: 1em;
      font-family: "Poppins", sans-serif !important;
      &+.btn-link{
        &:before{
          content: '/';
        }
      }
    }
  }
}
.discount{
  &-label{
    color: $black-medium;
    font-size: $font16;
    font-weight: 500;
    @media (max-width: 767px) {
      width: 100%;
      margin-bottom: 8px;
    }
    .code{
      color: $blue-dark;
    }
  }
  &-apply{
    display: flex;
    border: 1px solid rgba(180, 180, 180, 0.7);
    border-radius: 6px;
    justify-content: flex-end;
    align-items: center;
    padding: 15px 20px;
    flex-wrap: wrap;
    position: relative;
    top: -4px;
    width: calc(100% - 40px);
    margin-left: 20px;
    background: #fff;
    @media (max-width: 1024px) {
      width: 100%;
      margin-left: 0;
    }
    @media (max-width: 767px) {
      flex-wrap: wrap;
      justify-content: space-between;
      padding: 15px;
      width: 100%;
      margin-left: 0;
    }
    .btn-primary{
      @include standardLinkNew($blue, $white);
      min-width: 104px;
      @media (max-width: 767px) {
        min-width: 90px;
      }
      &.Mui-disabled{
        background-color: $grey;
        border-color: $grey;
        color: #6A6A6A;
        pointer-events: none;
      }
    }
    &-input{
      @media (max-width: 767px) {
        width: calc(100% - 105px);
      }
    }
    &.applied{
      border-radius: 0;
      border-width: 1px 0 0 0;
      padding-top: 25px;
      padding-bottom: 30px;
      padding-right: 0;
      @media (max-width: 1024px) {
        padding-right: 20px;
      }
      @media (max-width: 767px) {
        padding-top: 20px;
        padding-bottom: 0;
        padding-right: 10px;
      }
      .coupon-buttons{
        @media (max-width: 1150px) {
          min-width: 215px;
          padding-right: 20px;
          padding-left: 15px;
        }
        @media (max-width: 767px) {
          padding-left: 0;
          padding-right: 0;
          min-width: 160px;
        }
      }
    }
  }
  &-price{
    color: $black;
    margin-right: 5px;
    font-weight: 500;
    font-size: 17px;
    @media (max-width: 767px) {
      margin-right: 0;
      font-size: 1em;
    }
  }
}
.multipack{
  &-cart{
    &-buttons{
      display: flex;
      align-items: center;
      order: 3;
      .devider{
        color: #707070;
        padding: 0 15px;
      }
      button{
        padding: 2px 0;
        text-transform: capitalize;
        min-width: auto;
        color: $blue;
        font-family: "Poppins", sans-serif !important;
        font-weight: 500;
      }
    }
    &-detail{
      background-color: #F2F2F2;
      border-top: 1px solid #e8e8e8;
      padding: 20px 35px 10px 35px;
      position: relative;
      @media (max-width: 767px) {
        padding: 30px 15px 15px 15px;
        margin-left: -20px !important;
        margin-right: -20px !important;
        max-width: calc(100% + 40px) !important;
        flex-basis: calc(100% + 40px) !important;
      }
      &-close{
        position: absolute !important;
        height: 30px;
        width: 30px;
        top: 5px;
        right: 5px;
        background-color: $white !important;
        border-radius: 50% !important;
        min-width: auto !important;
        box-shadow: 0px 2px 4px #00000033;
        @media (max-width: 767px) {
          height: 26px;
          width: 26px;
          top: 5px;
          right: 15px;
        }
        i{
          -webkit-text-stroke: 2.4px $white;
          font-size: 20px;
          @media (max-width: 767px) {
            font-size: 18px;
          }
        }
      }
      &-inner{
        max-height: 400px;
        @include scrollbars(4px, #ADADAD, #D6D6D6);
        overflow-y: auto;
        &>div:not(:last-child){
          margin-bottom: 15px;
          @media (max-width: 767px) {
            margin-bottom: 15px;
          }
        }
        .title-product, .count{
          font-size: $font14 !important;
          @media (max-width: 767px) {
            font-size: $font12 !important;
          }
        }
        .cart-summary-error{
          width: 100%;
        }
      }
      &-img{
        width: 85px;
        padding-right: 20px;
        @media (max-width: 767px) {
          width: 75px;
        }
        &+div{
          display: flex;
          flex-wrap: wrap;
          flex-flow: column;
          justify-content: center;
        }
        img{
          max-width: 100%;
        }
      }
      .count{
        margin-top: 10px;
      }
    }
  }
}