@import "./../../../styles/commonStyle/commonStyle.scss";
.quantity-count {
  &-contain {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 13px;
    @media (max-width: 767px) {
      font-size: $font14;
    }
    .size-wrapper {
      color: $color-white;
      font-size: $font16;
    }
    .price {
      font-weight: 600;
      line-height: $font16;
    }
  }
  &-wrapper {
    display: flex;
    cursor: default;
    width: 90px;
    justify-content: flex-end;
    .trash-icon {
      font-size: $font30;
      vertical-align: bottom;
      @media (max-width: 400px) {
        font-size: $font24;
      }
    }
    button {
      padding: 0 0 0 1px;
      color: $color-white;
      background: none !important;
      width: 24px;
      height: 24px;
      border: 1px solid $white;
      border-radius: 50%;
      min-width: auto;
      font-size: 12px;
      &[disabled] {
        color: $color-white;
      }
      &:focus{
        outline: -webkit-focus-ring-color auto 2px !important;
        outline-color: $color-black !important;
        border-radius: 2px;
      }
      .qty-icon {
        font-size: $font30;
        @media (max-width: 767px) {
          font-size: $font24;
          width: 28px;
          height: 28px;
        }
        @media (max-width: 400px) {
          font-size: $font20;
          width: 24px;
          height: 24px;
        }
      }
    }
    input {
      width: 50px;
      text-align: center;
      border: none;
      font-size: $font16;
      font-weight: 500;
      color: $color-white;
      background-color: transparent;
      @media (max-width: 1025px) {
        font-size: $font14;
      }
      @media (max-width: 1025px) {
        font-size: $font16;
      }
      @media (max-width: 767px) {
        width: 30px;
        pointer-events: none;
      }
      &:active,
      &:hover {
        border: none;
        outline: none;
      }
    }
  }
}
