@import "./../../../styles/commonStyle/commonStyle.scss";
.footer {
  padding-top: 25px;
  padding-bottom: 15px;
  background-image: linear-gradient(
    90deg,
    #cc0000 0%,
    #db4c11 56%,
    #e3771a 100%
  );
  position: relative;
  border-top: 10px solid $blue;
  @media (max-width: $mobile-max-width) {
    padding: 10px;
  }
  @media (max-width: 600px) {
    border-top: 5px solid $blue;
    padding-left: 30px;
    padding-top: 25px;
    padding-bottom: 20px;
  }
  .logo {
    max-width: 100%;
    @media (max-width: $mobile-max-width) {
      max-width: 90px;
    }
    @media (max-width: 959px) {
      max-height: 35px;
      max-width: 100%;
    }
    @media (max-width: 767px) {
      max-width: 120px;
    }
    &-row {
      @media (max-width: $mobile-max-width) {
        position: absolute;
        top: -30px;
        left: 50%;
        transform: translateX(-50%);
      }
      @media (max-width: 959px) {
        top: -32px;
      }
    }
  }
  .container-root {
    max-width: 1300px !important;
    @media (max-width: $mobile-max-width) {
      margin: 0 !important;
      padding: 0px !important;
      width: 100% !important;
    }
  }
  .sublinks {
    padding: 0 70px 0 0;
    display: flex;
    flex-wrap: wrap;
    @media (max-width: $mobile-max-width) {
      padding: 0;
    }
    li {
      width: 33.33%;
      padding: 5px 2px;
      @media (max-width: $mobile-max-width) {
        width: 50%;
      }
      a {
        color: $color-white;
        font-size: $font14;
        font-weight: 600;
        cursor: pointer;
        text-decoration: none;
        transition: all 0.5s ease;
        @media (max-width: $mobile-max-width) {
          font-size: 0.875em;
        }
        &:hover {
          color: $color-yellow;
        }
      }
    }
  }
  .social-links {
    display: inline-flex;
    padding: 0;
    li {
      padding: 2px 10px;
      vertical-align: top;
      &:last-child {
        padding-right: 0;
      }
      @media (max-width: $mobile-max-width) {
        padding: 2px 10px;
      }
      @media (max-width: 767px) {
        padding: 2px 8px;
      }
      svg {
        height: 30px;
        path {
          fill: $color-white;
          transition: all 0.5s ease;
        }
        &:hover {
          path {
            fill: $color-white;
          }
        }
      }
      span {
        z-index: -1;
        color: $color-black;
        background: #fff;
        position: absolute;
        left: -9999px;
      }
    }
  }
  .copyright {
    text-align: right;
    padding-top: 10px;
    &-text {
      color: $color-white;
      font-size: $font14;
      font-weight: 600;
      margin: 10px 0 0 0;
    }
    @media (max-width: 767px) {
      text-align: center;
      border-top: 1px solid #fff;
      margin: 15px 0 0 0;
      width: calc(100% - 30px);
    }
  }

  // to add footer space in mobile
  &-steps {
    @media (max-width: $mobile-max-width) {
      padding-bottom: 80px;
    }
  }
}
