@import "../../styles/commonStyle/commonStyle.scss";
.multipack {
  &-accordion {
    &-header {
      font-size: $font28;
      color: $purple-medium !important;
      font-family: "RailroadGothic", sans-serif;
      padding: 0 !important;
      flex-direction: row-reverse;
      @media (max-width: 767px) {
        font-size: $font22;
        min-height: auto !important;
      }
      .expandIcon {
        padding: 0;
        svg {
          color: $purple-medium;
          font-size: 40px;
        }
      }
    }
    &-content {
      margin: 0 !important;
      padding-left: 15px;
    }
    &-wrapper {
      box-shadow: none !important;
      margin: 0 !important;
    }
    &-body {
      flex-wrap: wrap;
      padding: 8px 40px 8px 10px !important;
      margin: -15px;
      @media (max-width: 1024px) {
        padding: 8px 10px 8px 10px !important;
        margin: -8px;
      }
      @media (max-width: 767px) {
        margin: -12px;
        padding-bottom: 20px !important;
      }
      & > div {
        padding: 15px;
        @media (max-width: 1024px) {
          padding: 8px;
          max-width: 33.33%;
          flex-basis: 33.33%;
        }
        @media (max-width: 991px) {
          max-width: 25%;
          flex-basis: 25%;
        }
        @media (max-width: 767px) {
          padding: 4px;
          max-width: 33.33%;
          flex-basis: 33.33%;
        }
        @media (max-width: 359px) {
          max-width: 50%;
          flex-basis: 50%;
        }
      }
      .no-products{
        text-align: center;
        width: 100%;
        @media (max-width: 1024px) {
          max-width: 100%;
          flex-basis: 100%;
        }
      }
    }
  }
  &-tout {
    &.product-box-bundle-card {
      height: 250px;
      @media (max-width: 767px) {
        height: 175px;
      }
      .media-card-wrapper {
        height: 103px;
        margin: 5px 0 10px 0;
        width: 100%;
        @media all and (-ms-high-contrast:none) {
          margin: 5px 0 10px 10px;
        }
        @media (max-width: 767px) {
          height: 60px;
          margin: 5px 0 5px 0;
        }
        img {
          max-height: 100%;
          @media all and (-ms-high-contrast:none) {
            width: auto;
            max-width: 100%;
          }
        }
      }
      .brand{
        font-size: 13px;
        font-weight: 500;
        text-transform: capitalize;
        color: $black-medium;
        @media (max-width: 767px) {
          font-size: 11px;
        }
      }
      .product-heading {
        font-size: $font14;
        font-weight: 500;
        color: $black-medium;
        @media (max-width: 767px) {
          min-height: 30px;
        }
      }
    }
  }
}
