@import "./../../styles/commonStyle/commonStyle.scss";
.popular-products {
  .show-all-view {
    padding: 0 0 20px 0;
    @media (max-width: 1200px) {
      padding: 0 0 50px 0;
    }
  }
  &-horizontal {
    border-top: 1px solid $grey;
    &:first-child {
      border: 0;
    }
    &:nth-child(n + 5) {
      display: none;
    }
  }
  &-heading {
    h3 {
      @include popupHeading;
      @media (max-width: 767px) {
        font-size: $font24;
      }
    }
    &.heading-horizontal {
      .sticky-title-wrapper{
        justify-content: center;
      }
      h3 {
        font-size: $font14;
        color: $black;
        font-weight: 600;
        text-align: center;
        display: block;
        margin: 20px 0;
        font-family: "Poppins", sans-serif !important;
      }
    }
  }

  &-container {
    display: block;
    width: 100%;
    &-inner {
      max-width: $container-inner-width;
      flex-wrap: wrap;
      margin: 0 auto;
      display: flex;
    }
  }
  // &-show-all {
  //   display: flex;
  //   flex-wrap: wrap;
  //   .bundle-card {
  //     width: 92%;
  //     height: auto;
  //     margin: 4% 1% !important;
  //     // @media (max-width: 991px) {
  //     //   width: 31%;
  //     // }
  //     // @media (max-width: 767px) {
  //     //   margin: 3% !important;
  //     //   width: 44%;
  //     // }
  //   }
  // }
  &-slider {
    .rec {
      &-arrow {
        &:after {
          border-color: transparent transparent $color-orange $color-orange;
          box-shadow: -3px 3px 3px 0 $color-box-shadow;
        }
      }
    }
  }
  &-list {
    position: relative;
    display: flex;
    margin-bottom: 30px;
    width: 100%;
    justify-content: center;
    &>div{
      &>div{
        @media (max-width: 991px) {
          max-width: 33.33%;
          flex-basis: 33.33%;
        }
        @media (max-width: 767px) {
          max-width: 50%;
          flex-basis: 50%;
          padding: 4px !important;
        }
        @media (max-width: 359px) {
          max-width: 100%;
          flex-basis: 100%;
        }
      }
    }
  }
}
.home-wrapper{
  .popular-products{
    @media (max-width: 767px) {
      padding-top: 30px;
    }
    &-container-inner{
      @media (max-width: 767px) {
        position: relative;
        padding-bottom: 80px;
      }
      .heading{
        @media (max-width: 767px) {
          position: unset;
        }
        .show-all{
          min-width: 140px;
          text-transform: capitalize;
          &:hover{
            border-color: $blue;
          }
          @media (max-width: 767px) {
            position: absolute;
            bottom: 30px;
            color: $black-medium;
            border-color: $blue;
            text-transform: uppercase;
            &:hover{
              color: $white;
            }
          }
        }
      }
    }
    &-heading{
      .sticky-title-wrapper{
        @media (min-width: 767px) {
          margin: 0 3rem 1rem 0;
        }
      }
      h3{
        @media (min-width: 767px) {
          color: $white;
        }
      }
    }
  }
}