@import "../../styles/commonStyle/commonStyle.scss";
.multipack {
  &-tout {
    .more-vert{
      position: absolute;
      top: 0;
      right: 0;
      z-index: 2;
      @media (max-width: 767px) {
        padding: 2px 0 0 0;
        svg{
          font-size: 20px;
        }
      }
    }
    .quantity-count {
      &-wrapper {
        width: 100%;
        justify-content: center;
        button {
          min-width: auto;
          color: $black-medium;
          border: 2px solid $black-medium;
          border-radius: 50%;
          height: 32px;
          width: 32px;
          padding: 0 0 0 1px;
          @media (max-width: 767px) {
            height: 26px;
            width: 26px;
          }
          &.Mui-disabled{
            color: rgba(0,0,0,0.6);
            border-color: rgba(0,0,0,0.6);
          }
        }
        input {
          width: 40px;
          text-align: center;
          border: none;
          background-color: transparent;
          color: $black-medium;
          font-size: $font20;
          font-weight: 500;
          font-family: "Poppins", sans-serif;
          @media (max-width: 767px) {
            width: 32px;
          }
        }
      }
    }
    &.horizontal-view {
      height: auto !important;
      border: 0;
      .product-content-wrapper {
        display: flex;
      }
    }
  }
}
