@import "../../styles/commonStyle/commonStyle.scss";
.multipack {
  &-back{
    color: $white;
    position: relative;
    padding-left: 30px;
    text-decoration: none;
    font-size: 1em;
    font-weight: 500;
    padding-right: 15px;
    display: inline-flex;
    align-items: center;
    margin-bottom: 10px;
    @media (max-width: 1200px) {
      padding-left: 0;
    }
    @media (max-width: 767px) {
      padding: 0;
    }
    svg{
      font-size: 40px;
    }
  }
  &-header {
    background: transparent linear-gradient(0deg, $purple-dark 0%, $purple 100%) 0% 0%
      no-repeat;
    padding: 15px 35px 20px 35px;
    transition: all 0.3s;
    @media (max-width: 767px) {
      padding: 15px 16px 0 16px !important ;
      justify-content: flex-start !important;
      align-items: flex-start !important;
    }
    @media (max-width: 374px) {
      padding: 15px 10px 0 10px !important ;
    }
    &>div{
      &:first-child{
        @media (max-width: 767px) {
          width: 100%;
        }
      }
    }
    &.sticky-head {
      border-radius: 0;
      min-height: 275px;
      @media (max-width: 1366px) {
        min-height: 240px;
      }
      & + div {
        padding-top: 300px !important;
        @media (max-width: 1366px) {
          padding-top: 260px !important;
        }
        @media (max-width: 1024px) {
          padding-top: 270px !important;
        }
        @media (max-width: 767px) {
          padding-top: 330px !important;
        }
      }
      &.smaller{
        &+div{
          padding-top: 180px !important;
        }
      }
    }
    &.medium{
      min-height: 190px;
      @media (max-width: 1366px) {
        min-height: 190px;
      }
      h1{
        @media (min-width: 1366px){
          font-size: $font42;
        }
        @media (min-width: 767px) and (max-width: 1366px) {
          font-size: $font32;
        }
      }
      p{
        display: none;
      }
      .multipack-header-img {
        img{
          @media (min-width: 767px) {
            max-height: 120px;
          }
        }
        .price, .count{
          @media (min-width: 767px) {
            font-size: $font14;
          }
        }
      }
      .multipack-header-content{
        .search-bar{
          width: calc(100% - 250px);
          @media (max-width: 1200px) {
            width: 100%;
          }
          div[class*=-underline]{
            margin-top: 0;
          }
        }
      }
    }
    &.smaller{
      min-height: 76px;
      @media (max-width: 767px) {
        padding: 5px 16px 0 16px !important;
        align-items: center !important;
      }
      .multipack-back{
        margin-bottom: 0;
        span{
          @media (max-width: 767px) {
            display: none;
          }
        }
      }
      h1{
        display: none;
      }
      p{
        display: none;
      }
      .multipack-header-img{
        display: none;
      }
      &>div{
        max-width: 50%;
        flex-basis: 50%;
        @media (max-width: 767px) {
          max-width: 50px;
          flex-basis: 50px;
        }
      }
      .multipack-header-content{
        padding-right: 10px;
        padding-bottom: 0;
        justify-content: flex-end;
        flex-flow: row;
        @media (max-width: 767px) {
          max-width: calc(100% - 50px);
          flex-basis: calc(100% - 50px);
          position: relative;
          top: -5px;
          padding-top: 0;
        }
        .search-bar{
          width: 400px;
          @media (max-width: 767px) {
            width: 105px;
            position: static;
            &.focused{
              width: 100%;
            }
            &:not(.focused){
              margin-top: 8px;
              div[class*=-underline]{
                &:before{
                  opacity: 0;
                }
              }  
            }
          }
          div[class*=-underline]{
            margin-top: 0;
          }
        }
      }
    }
    &-img {
      max-width: 445px !important;
      flex-basis: 445px !important;
      padding-left: 45px;
      text-align: right;
      display: flex;
      align-items: flex-end;
      justify-content: flex-end;
      @media (max-width: 1024px) {
        max-width: 345px !important;
        flex-basis: 345px !important;
        padding-left: 15px;
      }
      @media (max-width: 991px) {
        max-width: 315px !important;
        flex-basis: 315px !important;
      }
      @media (max-width: 767px) {
        max-width: 150px !important;
        flex-basis: 150px !important;
        flex-direction: column-reverse;
        padding-left: 0;
        justify-content: center;
        align-items: center;
        padding-top: 30px;
        padding-bottom: 65px;
      }
      @media (max-width: 359px) {
        max-width: 120px !important;
        flex-basis: 120px !important;
      }
      .thumbnail{
        @media (max-width: 767px) {
          flex: 1
        }
      }
      img {
        max-width: 100%;
        max-height: 190px;
        margin-left: 10px;
        @media (max-width: 1366px) {
          max-height: 160px;
        }
        @media (max-width: 1024px) {
          max-height: 130px;
        }
        @media (max-width: 767px) {
          max-height: 130px;
          margin-left: 0;
        }
      }
      .price,
      .count {
        font-size: $font14;
        font-weight: 500;
        color: $white;
        @media (max-width: 767px) {
          font-size: 13px;
        }
      }
      .price{
        &-wrapper{
          padding-bottom: 12px;
          @media (max-width: 1200px) {
            padding-bottom: 0;
          }
          @media (max-width: 767px) {
            position: relative;
            top: -12px;
          }
        }
        &::after{
          content: "|";
          margin-left: 5px;
          margin-right: 5px;
        }
      }
    }
    &-content {
      padding-left: 35px;
      padding-right: 85px;
      padding-bottom: 15px;
      display: flex;
      flex-flow: column;
      @media (max-width: 1024px) {
        padding-right: 30px;
      }
      @media (max-width: 991px) {
        padding-left: 15px;
        padding-right: 0;
      }
      @media (max-width: 767px) {
        padding-left: 15px;
        padding-right: 15px;
        padding-top: 35px;
        padding-bottom: 65px;
      }
      h1 {
        font-size: $font52;
        color: $white;
        margin-top: 5px;
        margin-bottom: 5px;
        @media (max-width: 1366px) {
          font-size: $font36;
        }
        @media (max-width: 991px) {
          font-size: $font30;
        }
        @media (max-width: 767px) {
          text-align: center;
          position: absolute;
          top: 55px;
          width: 100%;
          left: 0;
          font-size: $font28;
        }
        @media (max-width: 359px) {
          font-size: $font26;
        }
      }
      p {
        color: $white;
        margin: 0;
        font-size: 19px;
        letter-spacing: 0.54px;
        @media (max-width: 1366px) {
          font-size: 16px;
        }
        @media (max-width: 991px) {
          font-size: 14px;
        }
        @media (max-width: 359px) {
          font-size: 13px;
        }
      }
      .search-bar{
        width: calc(100% - 100px);
        @media (max-width: 1024px) {
          width: 100%;
        }
        @media (max-width: 767px) {
          position: absolute;
          bottom: 18px;
          left: 40px;
          width: calc(100% - 80px);
        }
        @media (max-width: 359px) {
          left: 20px;
          width: calc(100% - 40px);
        }
        label{
          position: absolute;
          opacity: 0;
        }
        div[class*=-underline]{
          color: $white;
          margin-top: 20px;
          &:before, &:after{
            border-color: $white;
          }
          input{
            font-family: "Poppins", sans-serif !important;
            font-weight: 500;
            padding-left: 5px;
            padding-bottom: 10px;
            &::placeholder {
              opacity: 1;
            }
          }
        }
      }
    }
  }
}
