@import "./../../styles/commonStyle/commonStyle.scss";
.profile {
  &-heading {
    background-color: $white;
    @include boxShadowContainer;
    &-inner {
      margin: 0 auto !important;
      display: flex;
      padding: 55px 16px 45px;
      align-items: flex-end;
      // padding-top: 18px;
      justify-content: space-between;
      max-width: $container-inner-width;
      // @include boxShadowContainer;
      @media (max-width: 767px) {
        min-height: 86px;
        padding: 20px 16px;
        align-items: center;
        justify-content: center;
        flex-flow: column;
      }
      .button-group {
        display: flex;
        justify-content: flex-end;
        .disable-btn {
          background-color: $grey;
          color: $black;
          pointer-events: none;
        }
      }
      .page-heading {
        @include pageHeading;
      }
    }
  }
  .button-group {
    display: flex;
    justify-content: flex-end;
    .disable-btn {
      background-color: $grey;
      color: $black;
      pointer-events: none;
    }
  }
  .page-heading {
    @include pageHeading;
  }
  &-root {
    @media (max-width: 499px) {
      width: calc(100% + 30px);
      position: relative;
      left: -15px;
      padding: 0 10px;
    }
    @media (max-width: 413px) {
      padding: 0 10px;
    }
    @media (max-width: 359px) {
      padding: 0 5px;
    }
  }
  &-flexContainer {
    margin-top: 0px;
    justify-content: center;
    @media (max-width: 767px) {
      padding: 0 10px;
    }
    @media (max-width: 499px) {
      justify-content: center;
      padding: 0;
    }
    button {
      font-size: $font18;
      max-width: 400px;
      min-width: 250px;
      text-transform: uppercase;
      padding: 20px 35px;
      color: #4b4b4b;
      opacity: 1;
      font-weight: 600;
      margin: 0 15px;
      @media (max-width: $mobile-max-width) {
        font-size: 20px;
        min-width: auto;
        padding-left: 5px;
        padding-right: 5px;
        margin: 0;
        width: 33.33%;
      }
      @media (max-width: 767px) {
        max-width: none;
        font-size: $font16;
        padding: 10px;
      }
      @media (max-width: 499px) {
        width: auto;
        font-size: 13px;
      }
      @media (max-width: 413px) {
        padding: 10px 5px;
      }
      @media (max-width: 359px) {
        width: 100px;
        padding-left: 15px;
        padding-right: 15px;
      }
      &.Mui-selected {
        background: #ffffff 0% 0% no-repeat padding-box;
        opacity: 1;
        color: #0289ba;
      }
      &:nth-child(2) {
        @media (max-width: 499px) {
          // margin: 0 10px;
        }
      }
    }
  }
  &-indicator {
    background: #fff !important;
    height: 6px !important;
    border-radius: 10px;
    bottom: 15px !important;
    display: none;
    @media (max-width: $mobile-max-width) {
      height: 3px !important;
    }
  }
  &-info {
    margin-bottom: 25px;
    &:empty {
      display: none;
    }
    &.has-contact {
      margin-bottom: 0;
      .profile-card {
        margin-top: 25px;
      }
    }
    &.has-password {
      .profile-card {
        margin-top: 45px;
      }
    }
    &.order-history {
      background: $white;
      margin-top: 50px;
      box-shadow: 0px 2px 4px #00000033 !important;
      margin-bottom: 60px;
      @media (max-width: 767px) {
        background: transparent;
        box-shadow: none !important;
      }
      .title-mini {
        border-bottom: 1px solid #3330;
        padding-bottom: 20px;
        @media (min-width: $mobile-max-width) {
          width: calc(100% + 20px);
        }
        @media (max-width: 767px) {
          margin-bottom: 0;
        }
        & > div {
          &:first-child {
            @media (min-width: $mobile-max-width) {
              padding-left: 0;
            }
          }
          &:last-child {
            @media (min-width: $mobile-max-width) {
              padding-right: 0;
            }
          }
        }
        div.phone-select {
          select {
            background-color: #fff;
            padding-right: 35px !important;
          }
          &:after {
            right: 20px;
          }
        }
      }
      .pro-order-title {
        @media (max-width: 767px) {
          background: $white;
          border-radius: 6px;
          box-shadow: 0px 2px 4px #00000033;
          padding-top: 10px;
          padding-bottom: 10px;
        }
        &>div{
          &:first-child{
            max-width: none;
            flex-basis: auto;
          }
          &:last-child{
            max-width: 235px;
            flex-basis: 235px;
            @media (max-width: 767px) {
              max-width: 100%;
              flex-basis: 100%;
            }
          }
        }
        h2{
          font-size: $font18;
          font-weight: 500;
          color: $black-light;
        }
      }
      .history-card {
        box-shadow: none !important;
        border: none;
        padding: 0;
        margin-top: 20px;
        border-radius: 0 !important;
        @media (max-width: 767px) {
          padding-bottom: 15px;
          padding-top: 15px;
          margin-top: 0;
          &:not(:first-child) {
            border-top: 1px solid #333;
          }
        }
        &:first-child {
          margin-top: 0;
        }
        .key {
          color: #545454;
          font-size: 18px;
          font-family: "Lato", sans-serif !important;
          margin-bottom: 8px;
          @media (max-width: $mobile-max-width) {
            font-size: 16px;
          }
        }
        .val {
          color: #545454;
          font-size: 18px;
          font-family: "Lato", sans-serif !important;
          @media (max-width: $mobile-max-width) {
            font-size: 16px;
          }
        }
        .highlighed {
          color: $color-primary;
          font-size: 18px;
          border-bottom: 1px solid $color-primary;
          display: inline-block;
          font-weight: 600;
          font-family: "Lato", sans-serif !important;
          margin-bottom: 8px;
          text-decoration: none;
          @media (max-width: $mobile-max-width) {
            font-size: 16px;
          }
          .btn-link {
            border: none;
            @media (max-width: $mobile-max-width) {
              font-size: 16px;
            }
            i {
              margin-left: 5px;
              font-size: 24px;
              -webkit-text-stroke: 1.2px #fff;
            }
          }
        }
        & > div {
          &:first-child {
            flex-grow: 0;
            max-width: calc(100% - 555px);
            flex-basis: calc(100% - 555px);
            @media (max-width: 768px) {
              max-width: 100%;
              flex-basis: 100%;
            }
          }
          &:last-child {
            text-align: center;
            flex-grow: 0;
            max-width: 200px;
            flex-basis: 200px;
            @media (max-width: 768px) {
              max-width: 33.33%;
              flex-basis: 33.33%;
            }
            @media (max-width: 767px) {
              order: 3;
              max-width: 50%;
              flex-basis: 50%;
            }
          }
          &:nth-child(2) {
            flex-grow: 0;
            max-width: 140px;
            flex-basis: 140px;
            @media (max-width: 768px) {
              max-width: 33.33%;
              flex-basis: 33.33%;
            }
            @media (max-width: 767px) {
              order: 2;
              max-width: 50%;
              flex-basis: 50%;
            }
          }
          &:nth-child(3) {
            flex-grow: 0;
            max-width: 215px;
            flex-basis: 215px;
            padding-right: 0;
            @media (max-width: 768px) {
              max-width: 33.33%;
              flex-basis: 33.33%;
            }
            @media (max-width: 767px) {
              order: 4;
              max-width: 100%;
              flex-basis: 100%;
            }
            .highlighed {
              font-size: 17px;
            }
          }
        }
        .profile-card-single {
          border-right: none;
          padding: 0;
          @media (max-width: $mobile-max-width) {
            border-bottom: none;
          }
        }
        .order-status {
          font-size: 18px;
          font-weight: 400;
          @media (max-width: $mobile-max-width) {
            font-size: 16px;
          }
        }
      }
      .order-history {
        &-content {
          min-height: 300px;
          position: relative;
          padding-bottom: 40px;
          @media (max-width: 767px) {
            padding-bottom: 15px;
          }
          &:after {
            content: "";
            height: 1px;
            width: calc(100% + 20px);
            background-color: #333;
            position: absolute;
            left: -8px;
            bottom: 0;
          }
          .ordered-cart {
            border: 1px solid #ccc;
            background-color: #fff;
            border-radius: 8px;
            height: 0;
            transition-duration: 254ms;
            height: 0px;
            visibility: hidden;
            overflow: hidden;
            transition: height 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
            & > div {
              @media (max-width: 767px) {
                padding: 15px 0 15px 10px;
              }
            }
            &.open {
              height: auto;
              visibility: visible;
              margin-top: 20px;
              @media (max-width: 767px) {
                margin-top: 0;
                margin-bottom: 15px;
              }
            }
          }
        }
        &-action {
          text-align: right;
          padding: 50px 40px 20px 0;
          @media (max-width: 767px) {
            padding: 20px 0;
            text-align: center;
            a{
              display: inline-block;
              width: auto;
            }
          }
          .btn-outlined {
            font-size: 24px;
            border-color: $color-primary;
            color: $color-primary;
            border-width: 2px;
            border-radius: 30px;
            padding: 6px 20px;
            text-decoration: none;
            @media (max-width: 767px) {
              font-size: 18px;
              padding: 5px 15px;
            }
            @media (max-width: 374px) {
              font-size: 16px;
              padding: 5px 10px;
            }
            &:hover,
            &:focus {
              background-color: $color-primary;
              color: #fff;
            }
          }
        }
        &-accord {
          .ordered-cart {
            height: auto;
            visibility: visible;
            margin-top: 20px;
            background-color: #fff;
            border-radius: 0px;
            transition-duration: 254ms;
            overflow: hidden;
            transition: height 0.3s cubic-bezier(0.4, 0, 0.2, 1) 0ms;
            margin: 40px 60px;
            @media (max-width: 991px) {
              margin: 20px 15px;
            }
            @media (max-width: 767px) {
              margin: 0;
              background-color: #f2f2f2;
            }
            .reorder-bottom-mob {
              display: none;
              text-align: center;
              margin-bottom: 15px;
              .btn-link {
                padding-right: 0;
              }
              @media (max-width: 767px) {
                display: block;
              }
            }
          }
          .bundle-card-summary {
            &:not(:last-child) {
              position: relative;
            }
            &.is-title div {
              color: #343434 !important;
              font-weight: 600 !important;
            }
            .multipack{
              &-view-detail{
                padding: 0 !important;
                text-transform: capitalize;
                min-width: auto;
                color: $blue;
                font-family: "Poppins", sans-serif !important;
                font-weight: 500;
                @media (max-width: 767px) {
                  min-width: 110px;
                  text-align: left;
                  margin-top: 7px;
                  justify-content: flex-start;
                  font-size: $font14;
                }
              }
              &-cart-detail{
                @media (max-width: 767px) {
                  background-color: #fff;
                  margin-left: -10px !important;
                  margin-right: -10px !important;
                  max-width: calc(100% + 10px) !important;
                  flex-basis: calc(100% + 10px) !important;
                }
                &-inner{
                  padding: 20px 30px;
                  @media (max-width: 767px) {
                    padding: 20px 30px;
                    max-height: none;
                  }
                }
                &-close{
                  position: absolute;
                  top: 5px;
                  right: 15px;
                }
              }
            }
          }
          .summary-row-img {
            flex-grow: 0;
            max-width: 130px;
            flex-basis: 130px;
            @media (max-width: 767px) {
              max-width: 80px;
              flex-basis: 80px;
            }
          }
          .summary-img {
            padding: 20px;
            @media (max-width: 767px) {
              padding: 5px;
            }
            img {
              max-width: 100%;
              vertical-align: middle;
              font-size: 0.875em;
              line-height: 1em;
            }
          }
          .is-title {
            background-color: #efefef;
          }
          li {
            &.date {
              @media (max-width: 991px) {
                width: 110px;
              }
              @media (max-width: 767px) {
                width: 100%;
              }
            }
            &.order-number {
              @media (max-width: 991px) {
                width: 150px;
              }
              @media (max-width: 767px) {
                width: 100%;
              }
            }
            &.trck-no {
              @media (max-width: 991px) {
                flex: 1;
              }
              @media (max-width: 767px) {
                flex: none;
                width: 100%;
              }
              button {
                padding: 0;
              }
            }
            &.total {
              @media (max-width: 991px) {
                width: 100px;
              }
              @media (max-width: 767px) {
                width: 100%;
              }
            }
            &.ord-detail {
              @media (max-width: 991px) {
                width: 160px;
              }
              @media (max-width: 767px) {
                width: 100%;
                border-bottom: none;
              }
            }
          }
        }
      }
      .bundle-card-summary {
        @media (max-width: 767px) {
          align-items: flex-start;
        }
        &.is-title {
          padding-top: 5px;
          @media (max-width: 767px) {
            display: none;
          }
          div {
            color: $color-primary;
            font-weight: 600;
          }
          .text-right {
            padding-right: 25px;
          }
        }
        &:not(:last-child) {
          position: relative;
          @media (max-width: 767px) {
            padding-bottom: 5px;
            margin-bottom: 15px;
          }
          &:after {
            content: "";
            height: 1px;
            width: calc(100% - 16px);
            background: #e0e0e0;
            margin-left: 8px;
            position: absolute;
            bottom: 0;
            left: 0;
            @media (max-width: 767px) {
              margin-left: -2px;
              width: calc(100% + 2px);
            }
          }
        }
        .title-mobile {
          display: none;
          color: $black-light;
          padding-right: 5px;
          font-size: $font14;
          font-weight: 400;
          @media (max-width: 767px) {
            display: inline-block;
          }
        }
        .summary-row-info {
          flex-grow: 0;
          max-width: calc(100% - 130px);
          flex-basis: calc(100% - 130px);
          @media (max-width: 767px) {
            max-width: calc(100% - 80px);
            flex-basis: calc(100% - 80px);
            & > div {
              & > div {
                &:nth-child(1) {
                  max-width: 100%;
                  flex-basis: 100%;
                  margin-bottom: 10px;
                }
                &:nth-child(2) {
                  max-width: 50%;
                  flex-basis: 50%;
                }
                &:nth-child(3) {
                  max-width: 50%;
                  flex-basis: 50%;
                }
                &:nth-child(4) {
                  display: none;
                }
              }
            }
          }
        }
        .title-product {
          color: $color-grey-dark;
          font-size: $font18;
          line-height: $font18;
          @media (max-width: 767px) {
            font-size: $font14;
          }
        }
        .price {
          color: #231f20;
          font-weight: 600;
          font-size: $font20;
          line-height: 1.55;
          @media (max-width: 767px) {
            text-align: left;
            font-size: $font16;
            font-weight: 400;
            color: $black-light;
            .title-mobile {
              & + span {
                font-size: $font14;
              }
            }
          }
        }
        .qty-value {
          text-align: center;
          @media (max-width: 767px) {
            text-align: left;
            display: inline-block;
            font-size: $font14;
            font-weight: 400;
          }
        }
        input {
          background-color: transparent;
          @media (max-width: 767px) {
            text-align: left;
            width: 100px;
            font-size: $font16;
          }
        }
      }
      .acc {
        &-heading {
          ul {
            display: flex;
            list-style: none;
            padding: 0;
            margin: 0;
            background-color: #f2f2f2;
            border-top: 1px solid #c9c9c9;
            @media (max-width: 767px) {
              display: none;
            }
            li {
              width: 25%;
              padding: 18px 15px 25px;
              color: #343434;
              font-weight: 600;
              @media (max-width: 991px) {
                padding: 8px 8px;
              }
            }
          }
        }
        &-body {
          @media (max-width: 767px) {
            background-color: $white;
            margin-top: 20px;
            box-shadow: 0px 2px 4px #00000033;
            border-radius: 6px;
          }
          ul {
            display: flex;
            list-style: none;
            padding: 0;
            margin: 0;
            background-color: #f2f2f2;
            @media (max-width: 767px) {
              flex-wrap: wrap;
              background-color: transparent;
              padding: 0 10px;
            }
            li {
              width: 25%;
              padding: 14px 15px;
              color: #343434;
              font-weight: 400;
              border-bottom: 1px solid #ccc;
              @media (max-width: 991px) {
                padding: 8px 8px;
                border-bottom: 1px solid rgba(172, 172, 172, 0.6);
              }
              @media (max-width: 767px) {
                display: flex;
                justify-content: space-between;
              }
              .mob-title {
                display: none;
                color: $black-light;
                @media (max-width: 767px) {
                  display: block;
                }
              }
              &.ord-detail {
                button {
                  position: relative;
                  color: #0289ba;
                  padding: 0 35px 0 0;
                  text-transform: capitalize;
                  font-size: $font16;
                  &:after {
                    position: absolute;
                    right: 10px;
                    top: 4px;
                    content: "";
                    border: solid #707070;
                    border-width: 0 2px 2px 0;
                    display: inline-block;
                    padding: 6px;
                    transform: rotate(45deg);
                    cursor: pointer;
                  }
                }
              }
            }
          }
          &.show-div {
            .acc-content {
              display: block;
              background-color: #fff;
              min-height: 100px;
              padding: 20px 40px;
            }
            ul {
              background-color: #ffce11;
              @media (max-width: 767px) {
                background-color: transparent;
              }
            }
            .ord-detail {
              button {
                &::after {
                  transform: rotate(-135deg);
                  top: 10px;
                }
              }
            }
          }
        }
        &-content {
          display: none;
        }
      }
    }
    &-inline {
      text-align: right;
      margin-top: 25px;
      margin-bottom: 0px;
    }
    .title-mini {
      padding-left: 34px;
      @media (max-width: 991px) {
        padding-left: 15px;
      }
      @media (max-width: 767px) {
        padding-left: 5px;
      }
      &:not(:first-of-type) {
        margin-top: 40px;
      }
      &-shipping {
        & > div {
          @media (max-width: 359px) {
            flex-grow: 0;
            max-width: 100%;
            flex-basis: 100%;
          }
        }
      }
      h3,
      h2 {
        font-size: $font16;
        line-height: 1;
        font-family: "Poppins", sans-serif !important;
        font-weight: 600;
        letter-spacing: 0.4px;
        color: $black-medium;
        @media (max-width: 767px) {
          font-size: 18px;
        }
        @media (max-width: 420px) {
          font-size: 16px;
        }
      }
      .shipping-title {
        max-width: 300px;
        flex-basis: 300px;
      }
      .shipping-check {
        flex: 1;
        .checkBx {
          padding: 5px;
        }
        span {
          color: #0e2c4e;
          font-size: 18px;
          font-weight: 400;
        }
        path {
          fill: #000;
        }
      }
    }
    .shipping-note {
      font-size: 18px;
      font-weight: 400;
    }
    input {
      background-color: #fff;
    }
    .btn-link {
      font-size: 18px;
      padding-right: 15px;
      text-decoration: none;
      font-weight: 600;
      color: $orange-dark;
      @media (max-width: 767px) {
        font-size: 14px;
      }
      &:hover,
      &:focus {
        box-shadow: none;
        text-decoration: none;
        color: $color-primary-hover;
      }
      &:not(:last-child) {
        margin-right: 0px;
        @media (max-width: 767px) {
          margin-right: 5px;
        }
        &:after {
          padding-left: 15px;
          content: "|";
          @media (max-width: 767px) {
            padding-left: 5px;
          }
        }
      }
    }
    .btn-sm {
      border-radius: 50px;
      margin-top: 20px;
      padding-top: 0 !important;
      padding-bottom: 0 !important;
      font-size: 17px;
      @media (max-width: 767px) {
        padding: 2px 5px !important;
        font-size: 13px;
        min-width: 80px !important;
      }
      @media (max-width: 374px) {
        padding: 2px !important;
        min-width: 70px !important;
      }
      &:not(:last-child) {
        margin-right: 10px;
        @media (max-width: 767px) {
          margin-right: 5px;
        }
      }
      &.btn-blue {
        background-color: $color-primary;
        border-color: $color-primary;
        color: #fff;
        &:hover,
        &:focus {
          background-color: $color-primary-hover;
          border-color: $color-primary-hover;
        }
        &.Mui-disabled {
          border-color: $color-border;
          background-color: $color-border;
          color: $color-grey-dark;
        }
      }
    }
    .button-group {
      margin-top: 60px;
    }
    .react-select-big {
      & > span + div[class*="-control"]:after {
        border-width: 1px;
        height: 100%;
        width: 100%;
        border-radius: 8px;
        top: -1px;
        left: -1px;
      }
    }
  }
  &-card {
    padding: 40px;
    box-shadow: 0px 1px 3px #00000034 !important;
    border: 1px solid #dddddd;
    overflow: unset !important;
    @media (max-width: 991px) {
      padding: 25px 10px;
    }
    &-inn {
      display: flex;
      width: 100%;
      padding: 18px 0 0;
      @media (max-width: 991px) {
        flex-wrap: wrap;
      }
      @media (max-width: 767px) {
        padding: 5px 0 0;
      }
    }
    &.card-3 {
      display: flex;
      padding-top: 35px;
      flex-wrap: wrap;
      @media (max-width: $mobile-max-width) {
        flex-flow: column;
      }
      @media (max-width: 767px) {
        padding-top: 20px;
      }
      .profile-card-single {
        flex-grow: 0;
        max-width: 33.333333%;
        flex-basis: 33.333333%;
        &:nth-child(2) {
          flex-grow: 0;
          max-width: 40%;
          flex-basis: 40%;
          @media (max-width: $mobile-max-width) {
            max-width: 100%;
            flex-basis: 100%;
          }
        }
        @media (max-width: $mobile-max-width) {
          max-width: 100%;
          flex-basis: 100%;
        }
      }
    }
    &.card-2 {
      display: flex;
      .profile-card-single {
        flex-grow: 0;
        max-width: 50%;
        flex-basis: 50%;
      }
    }
    &-single {
      border-right: 1px solid #ccc;
      padding: 20px 34px;
      @media (max-width: $mobile-max-width) {
        padding: 15px;
        border-right: none;
        border-bottom: 1px solid #ccc;
      }
      @media (max-width: 767px) {
        padding: 15px 5px;
      }
      &:last-child {
        border-right: none;
        @media (max-width: $mobile-max-width) {
          border-bottom: none !important;
        }
      }
      span {
        svg {
          color: #707070;
        }
        &.Mui-checked {
          svg {
            color: $color-primary;
          }
        }
      }
    }
    &-form {
      padding: 40px;
      @media (max-width: $mobile-max-width) {
        padding: 30px 15px;
      }
      @media (max-width: 767px) {
        padding: 20px 15px;
      }
      .title-mini {
        padding-left: 0;
      }
      .form-card-container {
        padding-top: 50px;
        @media (max-width: $mobile-max-width) {
          padding-top: 25px;
        }
      }
      .password-note {
        color: $black-medium;
        font-size: 18px;
        padding-left: 15px;
        margin: 0;
        @media (max-width: 767px) {
          font-size: 16px;
        }
        li {
          margin-left: 20px;
        }
      }
    }
    &-bill,
    &-shipp {
      margin-top: 15px;
      @media (max-width: 767px) {
        margin-top: 5px;
      }
      .profile-card-single {
        border-bottom: none;
      }
    }
    .key {
      color: #333333;
      font-size: 18px;
      font-weight: 600;
      @media (max-width: 767px) {
        font-size: 16px;
      }
    }
    .val {
      color: #333333;
      font-size: 18px;
      word-wrap: break-word;
      @media (max-width: 767px) {
        font-size: 16px;
      }
      @media (max-width: 420px) {
        font-size: 14px;
      }
    }
    .form-card-container {
      @media (max-width: $mobile-max-width) {
        width: calc(100% + 20px) !important;
        margin: -10px !important;
      }
      & > div {
        @media (max-width: $mobile-max-width) {
          padding: 10px !important;
        }
      }
    }
    .form-group {
      input,
      select {
        @media (max-width: $mobile-max-width) {
          padding: 16.5px 15px;
        }
      }
      .form-label {
        @media (max-width: $mobile-max-width) {
          transform: translate(10px, 18px) scale(1);
        }
        &[class*="-shrink"] {
          @media (max-width: $mobile-max-width) {
            transform: translate(10px, -6px) scale(1);
          }
        }
      }
    }
  }
  &-dropdown{
    position: relative;
    @media (max-width: 767px) {
      width: 100%;
      margin-top: 10px !important;
    }
    .btn-primary{
      justify-content: flex-start;
      position: relative;
      text-transform: capitalize;
      display: none;
      @media (max-width: 767px) {
        display: flex;
      }
      &.open{
        border-radius: 6px 6px 0 0;
      }
      i{
        position: absolute;
        right: 20px;
        top: 5px;
        font-size: 34px;
        -webkit-text-stroke: 3px $blue;
      }
      &:hover{
        background-color: $blue !important;
        color: #fff !important;
      }
    }
    ul{
      position: absolute;
      top: 43px;
      left: 0;
      width: 100%;
      background: #fff;
      box-shadow: 0px 1px 3px #00000034 !important;
      text-transform: capitalize;
      padding: 0;
      border-radius: 0 0 6px 6px;
      li{
        font-size: 1em;
        padding: 10px 15px;
        min-height: auto;
      }
      li:not(:last-child){
        border-bottom: 1px solid rgba(0,0,0,0.1);
      }
    }
  }
}

.btn-bottom-row {
  padding-left: 35px;
  @media (max-width: 991px) {
    padding-left: 15px;
  }
  @media (max-width: 767px) {
    padding-left: 5px;
  }
}
.pro-order-detail {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 30px;
  @media (max-width: 767px) {
    margin-top: 15px;
  }
  & > div {
    @media (max-width: 991px) {
      padding: 0 5px;
    }
    @media (max-width: 767px) {
      width: 100% !important;
      padding: 10px 15px;
      &:not(:last-child) {
        border-bottom: 1px solid #ccc;
      }
    }
  }
  p {
    margin: 0;
    @media (max-width: 767px) {
      display: flex;
      justify-content: space-between;
      text-align: right;
    }
    strong {
      color: #343434;
      @media (max-width: 767px) {
        text-align: left;
        width: 150px;
      }
    }
  }
  .ord-date {
    width: 40%;
    @media (max-width: 991px) {
      width: 35%;
    }
    @media (max-width: 767px) {
      display: none;
    }
  }
  .ord-ship-info {
    width: 30%;
  }
  .ord-item-count {
    width: 20%;
  }
  .ord-item-total {
    @media (max-width: 767px) {
      display: none;
    }
  }
  .re-ord-btn {
    width: 10%;
    @media (max-width: 1200px){
      .btn-link{
        font-size: 16px;
      }
    }
    @media (max-width: 991px) {
      width: 15%;
    }
    @media (max-width: 767px) {
      display: none;
    }
  }
}
.tabMenu{
  background: $blue 0% 0% no-repeat padding-box;
  opacity: 1;
  @media (max-width: 767px) {
    display: none !important;
  }
}
.profile-page{
  .sticky-head{
    &+div {
      padding-top: 82px !important;
    }
    &.has-button + div {
      @media (max-width: 767px) {
        padding-top: 105px !important;
      }
    }
  }
}