@import "./../../../styles/commonStyle/commonStyle.scss";
div {
  &.accordion {
    &-wrapper {
      box-shadow: none;
      margin: 0 !important;
      background-color: transparent;
      &:before {
        opacity: 0;
      }
      p {
        color: $color-grey-dark;
        font-size: $font12;
      }
    }
    &-header {
      border: none;
      display: block;
      margin: 36px 0 0 0;
      padding: 0;
      min-height: auto !important;
      & > div {
        margin: 0 !important;
        padding: 0;
      }
      // .fa-angle-up {
      //   -webkit-text-stroke: 1.5px #fff;
      //   color: #02a6e1;
      //   font-size: 28px;
      // }
      div {
        display: inline-block;
        h2 {
          font-size: $font18;
          line-height: 1.56;
          margin-right: 14px;
          color: $white;
        }
        span {
          .fa {
            color: $white;
          }
        }
      }
    }
    &-body {
      padding: 10px 0;
      img {
        max-width: 80%;
        max-height: 300px;
      }
    }
  }
}
.product {
  &-detail {
    &-image {
      text-align: center;
      &-outer {
        min-height: 280px;
        @media (max-width: 767px) {
          min-height: auto;
        }
      }
      img {
        max-width: 100%;
        max-height: 100%;
      }
      .shadow {
        height: 54px;
        background: radial-gradient(
          ellipse at 50% 33%,
          rgba(0, 0, 0, 0.3),
          rgba(97, 97, 97, 0) 40%
        );
      }
    }
    &-wrapper {
      padding: 36px;
      border-radius: 10px;
      background-color: $black-light;
      .expand-more-icon {
        font-size: $font24;
        color: $white;
      }
      @media (max-width: 959px) {
        padding-top: 15px;
      }
      @media (max-width: $mobile-max-width) {
        position: relative;

        .product-info-container {
          & > div {
            width: 100%;
          }
          .product-detail-image {
            height: 182px;
            margin: 0 auto 24px auto;
            width: 195px;
          }
          .product-info-div {
            flex: unset;
            .product-brand {
              font-size: $font14;
            }
            .product-name {
              font-size: $font20;
            }
            .product-desc {
              font-size: $font12;
            }
          }
        }
      }
      @media (max-width: 959px) {
        padding: 30px;
      }
    }
    &-actions {
      padding: 5px 0 0 0;
      @media (max-width: 767px) {
        padding: 10px 0;
        height: auto;
      }
      @media all and (-ms-high-contrast: none) {
        display: flex;
        align-items: center;
      }
      .size-wrapper {
        max-width: 100%;
      }
      .quantity-count-wrapper {
        width: 118px;
        button {
          font-size: $font16;
          width: 34px;
          height: 34px;
          &.Mui-disabled{
            opacity: 0.6;
          }
        }
      }
    }
  }
  &-desc {
    font-size: $font14;
    line-height: 1.29;
    color: $white;
  }
  &-brand {
    font-size: $font18;
    line-height: 2;
    color: $white;
  }
  &-name {
    font-family: "Poppins", sans-serif !important;
    font-size: $font24;
    font-weight: 600;
    line-height: 1.17;
    color: $white;
    margin-bottom: 18px;
  }
}
